<!--

  Component:    AnnotationListControl
  Description:  Component to show annotations of a given plot id
                It provides delete annotation features if editable is true.

  Props:
   editable:   Enable deletion of annotations
   plotId:     Similarity plot id

-->

<template>
  <div>
    <!-- No annotations -->
    <div v-if="getSimilarityAnnotations[plotId].length===0">
      <div class="warning--text mb-2">No annotations added yet.</div>
    </div>

    <!-- List of grouped annotations -->
    <div v-else class="mb-1">
      <div v-for="[category,groupAnnotations] in groupAnnotations" :key="category" class="mb-4">
        <div class="text-body-3 mb-3">
          <v-icon left small>{{ categories[category].icon }}</v-icon>
          {{ categories[category].text }}
        </div>
        <v-tooltip v-for="{displayName,start,stop} in groupAnnotations" :key="displayName"
                   bottom :open-delay="200">
          <template v-slot:activator="{attrs,on}">
            <v-chip small v-bind="attrs" v-on="on" class="mr-1 mb-1">{{ displayName }}
              <v-icon v-if="editable" color="bg_text" right x-small @click="deannotate(displayName)">mdi-close</v-icon>
            </v-chip>
          </template>
          <div>
            <div>Start position: {{ start }}</div>
            <div>End position: {{ stop }}</div>
          </div>
        </v-tooltip>
      </div>

      <!-- Clear option -->
      <div v-if="editable">
        <btn-with-tooltip text="Clear annotations" size="small" hover-color="error" :click-handler="clearAll"
                          color="text_bg" content-class="bg--text" icon="mdi-trash-can-outline"/>
      </div>
    </div>

  </div>
</template>

<script>
import {mapGetters, mapMutations} from "vuex";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: "AnnotationListControl",
  components: {BtnWithTooltip},

  props: {
    /** Similarity plot id */
    plotId: {},

    /** Enable deletion of annotations */
    editable: Boolean,
  },

  data() {
    return {
      /** Categories mapping */
      categories: {
        prot: {text: 'Proteins annotations', icon: 'mdi-label-multiple-outline'},
      },
    }
  },

  computed: {
    ...mapGetters(['getSimilarityAnnotations']),

    /** Plot annotations organized in groups */
    groupAnnotations() {
      const groups = {}

      Object.values(this.getSimilarityAnnotations[this.plotId])
          .map(({category, ...data}) => {
            if (groups[category])
              groups[category].push(data)
            else
              groups[category] = [data]
          })
      return Object.entries(groups)
    }
  },

  methods: {
    ...mapMutations(['removeAnnotation','clearAnnotations']),

    /**
     * Remove a given annotation
     * @param annotation  Annotation name
     */
    deannotate(annotation) {
      this.removeAnnotation({plotId: this.plotId, annotation})
    },

    /**
     * Clear all the annotations
     */
    clearAll() {
      this.clearAnnotations(this.plotId)
    }
  }
}
</script>

