<!--

  Component:    SequenceUploader
  Description:  Uploader input element for sequence file

  Props:
   value:   Value of the content of the file

-->

<template>
  <v-col>

    <!-- Headings -->
    <v-row class="px-5 pb-2">
      <v-col>
        <div class="text-body-2 compact-text-2 text_bg--text">
          <span class="text-body-1 font-weight-bold tertiary--text">Sequence data:</span>
          upload the .fasta file of to the nucleotide sequences you want to analyze
        </div>
      </v-col>
    </v-row>

    <!-- Input element -->
    <v-row dense class="px-5">
      <v-col>
        <v-file-input v-model="uploadedFile" placeholder="FASTA file" accept=".fasta" dense outlined color="tertiary"
                      :loading="isLoading" @click:clear="clearFile" show-size/>
      </v-col>
    </v-row>

    <loading-sticker :is-loading="isLoading" :error="error"/>

  </v-col>
</template>

<script>
import LoadingSticker from "@/components/general/basic/LoadingSticker";

export default {
  name: 'SequenceUploader',
  components: {LoadingSticker},

  props:{
    /** Value of the content of the file */
    value: {}
  },

  data() {
    return {
      /** User uploaded file */
      uploadedFile: null,


      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed:{
    /** User uploaded file content  */
    uploadedFileContent: {
      set(newVal){
        this.$emit('input',newVal)
      },
      get(){
        return this.value
      }
    }

  },

  watch: {
    /** On file change, parse file */
    uploadedFile(newVal) {
      if(newVal)
          this.parseFile()
    }
  },

  methods: {

    /** Clear file */
    clearFile() {
      this.uploadedFile = null
      this.uploadedFileContent = null
    },

    /** Parse file and add content to the store */
    parseFile() {
      try {
        this.isLoading = true
        this.error = undefined

        // Actually read content
        const reader = new FileReader();
        reader.readAsText(this.uploadedFile)
        reader.onload = () => {
          this.uploadedFileContent = reader.result
        }
      } catch (e) {
        this.error = e
      } finally {
        this.isLoading = false
      }
    }
  }
}
</script>

