<!--

  View:         HomeView
  Description:  View container for the tool home

-->

<template>
  <div class="height-100">
    <!-- Tool sidebar -->
    <sidebar/>

    <!-- Main sub-view -->
    <div>
      <div class="tool-views-container height-100">
        <v-fade-transition hide-on-leave>
          <new-search-view v-if="currentView==='new-search'"/>
          <result-view v-if="currentView==='result'" id="to-print"/>
        </v-fade-transition>
      </div>
    </div>

  </div>
</template>

<script>
import Sidebar from "@/components/general/Sidebar";
import NewSearchView from "@/components/views/NewSearchView";
import ResultView from "@/components/views/ResultView";
import {mapState} from "vuex";

export default {
  name: "HomeView",
  components: {ResultView, NewSearchView, Sidebar},

  data() {
    return {
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    ...mapState(['currentAnalysisId', 'loading']),

    /** Current type of main sub-view */
    currentView() {
      return this.currentAnalysisId !== null ? 'result' : 'new-search'
    }
  }
}
</script>

<style scoped>
#to-print{
  background: var(--v-bg-base) !important; /** Background for screenshot feature */
}
.tool-views-container {
  background: var(--v-bg-base);
  margin-left: 56px;
  padding-bottom: 200px;
}
