<!--

  Component:    PlotPairLimitControl
  Description:  Component to change number of pairs to be shown

  Props:
   value:  Value limit

-->

<template>
  <v-menu v-model="showOptions" content-class="rounded-xl options-menu" offset-y open-on-click width="250px"
          max-width="80vw" :open-on-hover="false" :close-on-content-click="false" rounded>

    <!-- Activator: button -->
    <template v-slot:activator="{ attrs, on }">
        <span v-bind="attrs" v-on="on">
          <btn-with-tooltip bottom color="primary" outlined hover-color="secondary" size="small"
                            icon="mdi-eye-outline" text="Number of pairs"
                            :tip="showOptions?'':'Limit the number of lineage pairs in the plot'"
                            :click-handler="()=>showOptions=!showOptions"/>
        </span>
    </template>

    <!-- Actual menu with aggregation options-->
    <div @mouseleave="showOptions=false">
      <v-list color="bg" class="rounded-xl" rounded dense width="auto">
        <v-list-item dense class="align-baseline">
          <v-icon class="pr-3" color="tertiary">mdi-eye-outline</v-icon>
          <v-list-item-content>
            <v-list-item-title class="tertiary--text">Number of lineage pairs</v-list-item-title>
            <v-list-item-subtitle class="break-spaces">Limit the number of candidate recombinant lineage
              pairs to be shown in the plot
            </v-list-item-subtitle>
            <div>
              <v-form v-model="validForm" @submit.prevent="">
                <v-text-field v-model="limit" class="pt-0" placeholder="Max number" :rules="limitRules"/>
              </v-form>
            </div>
          </v-list-item-content>
        </v-list-item>
        <div class="d-flex mx-5 mb-2">
          <btn-with-tooltip :color="(validForm && Number(limit)!==-1)?undefined:'secondary'" text="Do not limit" icon="mdi-filter-off-outline"
                            size="x-small" :click-handler="noLimit"/>
          <v-spacer/>
          <btn-with-tooltip :color="(validForm && Number(limit)!==-1)?'secondary':undefined" text="Apply" icon="mdi-check" size="x-small"
                            :click-handler="apply" :disabled="!validForm"/>
        </div>
      </v-list>

    </div>
  </v-menu>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: "PlotPairLimitControl",
  components: {BtnWithTooltip},

  props: {
    /** Value of the aggregation mode */
    value: {},
  },

  data() {
    return {
      /** Boolean visibility flag for the aggregation options menu */
      showOptions: false,

      /** Limit value */
      limit: 10,

      limitRules: [
        val => !!val || 'Required.',
        val => (Number.isInteger(Number(val)) && (Number(val) > 0 || Number(val) === -1)) || 'Must be a positive integer or -1.',
      ],

      /** Boolean flag set to true if the form is valid */
      validForm: null,
    }
  },

  methods: {
    /**
     * Change the limit
     */
    apply() {
      this.$emit('input', Number(this.limit))
      this.showOptions=false
    },

    /**
     * Apply no limit
     */
    noLimit() {
      this.limit=-1
      this.$emit('input', Number(-1))
      this.showOptions=false
    }
  },

  mounted() {
    this.limit = this.value
  }
}
</script>

