<template>
<section-element title="Mutations" icon="mdi-source-branch" :subtitle="subtitle">
  <mutation-table mode="result"/>
</section-element>
</template>

<script>
import SectionElement from "@/components/analysis/SectionElement.vue";
import MutationTable from "@/components/form/MutationTable.vue";
import {mapGetters} from "vuex";

export default {
  name: "MutationTableSection",
  components: {MutationTable, SectionElement},

  computed:{
    ...mapGetters(['getCurrentAnalysis']),

    subtitle(){
      const mutL=this.getCurrentAnalysis.muts.length
      return mutL+ " mutation"+ (mutL !== 1 ? 's' : '')+" detected in the sequence"
    }
  }
}
</script>

