<!--

  Component:    DefinitionStep
  Description:  First step of the new analysis definition: sequence upload.

  Events:
   showFeatured:   Hide/show featured
   next:           On next step request

-->

<template>
  <v-container class="pt-10 definition-container">

    <!-- Select input mode -->
    <v-scroll-x-transition hide-on-leave>
      <v-row class="px-5 pb-2" v-if="!mode" no-gutters>
        <v-col class="mb-2">
          <div class="text-body-1 font-weight-bold tertiary--text">
            How do you want to load the sequence to be analyzed?
          </div>
        </v-col>
        <v-col cols="12">
          <v-list color="bg" class="rounded-xl" rounded width="100%">
            <v-list-item v-for="{id, title, text, icon} in options" :key="id" link @click="mode=id">
              <v-icon class="pr-3" color="tertiary" large left v-text="icon"/>
              <v-list-item-content>
                <v-list-item-title class="tertiary--text break-spaces font-weight-medium" v-text="title"/>
                <v-list-item-subtitle class="break-spaces" v-text="text"/>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-col>
      </v-row>
    </v-scroll-x-transition>

    <!-- Input mode -->
    <sequence-definition v-model="mode" @next="$emit('next')"/>

  </v-container>
</template>

<script>
import SequenceDefinition from "@/components/form/SequenceDefinition.vue";

export default {
  name: "DefinitionStep",
  components: {SequenceDefinition},

  data() {
    return {
      /** Options to load sequence */
      options: [
        {
          id: 'file',
          icon: 'mdi-file-document-outline',
          title: 'Upload .FASTA file directly',
          text: 'Directly upload a .FASTA file from your computer'
        },
        {
          id: 'id',
          icon: 'mdi-barcode-scan',
          title: 'Find sequence by id',
          text: 'Provide the identifier of a sequence from GISAID or GenBank'
        },
        {
          id: 'list',
          icon: 'mdi-format-list-bulleted-type',
          title: 'Enter mutation list',
          text: 'Provide a list of nucleotide mutations'
        },

      ],

      /** Currently selected mode or null */
      mode: null,
    }
  },

  watch:{
    mode(newVal){
      this.$emit('showFeatured',!newVal)
    }
  }
}
</script>

