<!--

  Component:    PlotSlider
  Description:  Component to change gene position range

  Props:
   range:  Current plot range

  Events:
   goTo:  Emitted on custom range selection

-->

<template>
  <!-- Current selection -->
  <div class="d-flex">
    <div>
      <v-icon small left color="text_dark">mdi-eye-outline</v-icon>
    </div>
    <div class="d-inline-block text-body-5 compact-text-3 ml-1">
      <div class="d-inline-block font-weight-medium mr-1">Showing range:</div>
      <div class="d-inline-block">[{{ Math.trunc(range.at(0)) }},{{ Math.trunc(range.at(1)) }}]</div>
      <div class="mt-1">
        <v-menu v-model="showOptions" content-class="rounded-xl options-menu" offset-y open-on-click width="250px"
                max-width="80vw" :open-on-hover="false" :close-on-content-click="false" rounded>

          <!-- Activator: button -->
          <template v-slot:activator="{ attrs, on }">
        <span v-bind="attrs" v-on="on">
          <btn-with-tooltip bottom color="text_dark" outlined hover-color="secondary" size="x-small"
                            text="Edit" :tip="showOptions?'':'Select specific gene position range'"
                            :click-handler="()=>showOptions=!showOptions"/>
        </span>
          </template>

          <!-- Actual menu with aggregation options-->
          <div @mouseleave="showOptions=false">
            <v-list color="bg" class="rounded-xl" rounded dense width="auto">
              <v-list-item dense class="align-baseline">
                <v-icon class="pr-3" color="tertiary">mdi-eye-outline</v-icon>
                <v-list-item-content>
                  <v-list-item-title class="tertiary--text">Range of positions</v-list-item-title>
                  <v-list-item-subtitle class="break-spaces">Interval of gene positions to be displayed
                  </v-list-item-subtitle>
                  <div>
                    <v-form v-model="validForm" @submit.prevent="">
                      <v-text-field v-model="start" class="pt-0" placeholder="Starting position" :rules="startRules"/>
                      <v-text-field v-model="stop" class="pt-0" placeholder="Stop position" :rules="stopRules"/>
                    </v-form>
                  </div>
                </v-list-item-content>
              </v-list-item>
              <div class="d-flex mx-5 mb-2">
                <btn-with-tooltip :color="validForm?undefined:'secondary'" text="Restore default"
                                  icon="mdi-filter-off-outline"
                                  size="x-small" :click-handler="noLimit"/>
                <v-spacer/>
                <btn-with-tooltip :color="validForm?'secondary':undefined" text="Apply" icon="mdi-check"
                                  size="x-small"
                                  :click-handler="apply" :disabled="!validForm"/>
              </div>
            </v-list>

          </div>
        </v-menu>

      </div>
    </div>
  </div>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import proteinsJSON from "@/utils/annotations/proteins.json";

export default {
  name: "PlotSlider",
  components: {BtnWithTooltip},

  props: {
    /** Current plot range */
    range: {},
  },

  data() {
    return {
      /** Boolean visibility flag for the aggregation options menu */
      showOptions: false,

      /** Custom range start */
      start: null,
      /** Custom range stop */
      stop: null,

      startRules: [
        val => (!!val || val === 0) || 'Required.',
        val => (Number.isInteger(Number(val)) && (Number(val) >= 0 || Number(val) < 29903)) || 'Must be a number within 0 and 29903.',
        val => (!this.stop || val < Number(this.stop)) || 'Must be smaller than ' + this.stop + '.',
      ],
      stopRules: [
        val => !!val || 'Required.',
        val => (Number.isInteger(Number(val)) && (Number(val) > 0 || Number(val) <= 29903)) || 'Must be a number within 0 and 29903.',
        val => (!this.start || val > Number(this.start)) || 'Must be smaller than ' + this.start + '.',
      ],

      /** Boolean flag set to true if the form is valid */
      validForm: null,
    }
  },

  watch: {
    range(newVal) {
      if (newVal) {
        this.start = Math.trunc(newVal.at(0))
        this.stop = Math.trunc(newVal.at(1))
      }
    }
  },

  methods: {
    /**
     * Change the limit
     */
    apply() {
      this.$emit('goTo', {
        "xaxis.range[0]": Number(this.start),
        "xaxis.range[1]": Number(this.stop)
      })
      this.showOptions = false
    },

    /**
     * Apply no limit
     */
    noLimit() {
      this.start = 0
      this.stop = 29903
      this.$emit('goTo', {
        "xaxis.range[0]": this.start,
        "xaxis.range[1]": this.stop
      })
      this.showOptions = false
    }
  },

  mounted() {
    this.start = Math.trunc(this.range.at(0))
    this.stop = Math.trunc(this.range.at(1))
  }
}
</script>

