<!--

  View:         ResultView
  Description:  Sub-view container for the result panel

-->

<template>
  <div class="view-sizing">
    <!-- Result toolbar with various options -->
    <result-navbar/>

    <!-- Actual result panel -->
    <v-container class="content-container" id="result-top">

      <!-- Info -->
      <v-slide-y-reverse-transition hide-on-leave>
        <result-info v-if="sections?.info"/>
        <v-skeleton-loader v-else type="list-item" height="200" class="mt-8"/>
      </v-slide-y-reverse-transition>

      <!-- Mut List -->
      <v-slide-y-reverse-transition hide-on-leave>
        <mutation-table-section v-if="sections?.muts"/>
        <v-skeleton-loader v-else type="list-item" height="200" class="mt-8"/>
      </v-slide-y-reverse-transition>

      <!-- Recurrence Plot -->
      <v-slide-y-reverse-transition hide-on-leave>
        <recurrence-plot v-if="sections?.recurrence"/>
        <v-skeleton-loader v-else type="image" height="200" class="mt-8"/>
      </v-slide-y-reverse-transition>

      <!-- Similarity Plots -->
      <v-slide-y-reverse-transition hide-on-leave>
        <similarity-plots-section v-if="sections?.similarity"/>
        <v-skeleton-loader v-else type="image" height="200" class="mt-8"/>
      </v-slide-y-reverse-transition>

      <!-- Local processing loading element -->
      <loading-sticker :is-loading="Object.values(sections).some(x=>!x)" no-overlay
                       :loading-messages="[{text:'Processing',time:3000}]"/>
    </v-container>
  </div>
</template>

<script>
import ResultNavbar from "@/components/analysis/navbar/ResultNavbar";
import LoadingSticker from "@/components/general/basic/LoadingSticker";
import RecurrencePlot from "@/components/analysis/plots/RecurrencePlot.vue";
import ResultInfo from "@/components/analysis/info/ResultInfo.vue";
import SimilarityPlotsSection from "@/components/analysis/SimilarityPlotsSection.vue";
import MutationTableSection from "@/components/analysis/MutationTableSection.vue";

export default {
  name: 'ResultView',
  components: {
    MutationTableSection,
    SimilarityPlotsSection,
    ResultInfo, RecurrencePlot, LoadingSticker, ResultNavbar,
  },

  data() {
    return {
      /** Visibility flags for the sections */
      sections: {
        'info': false,
        'muts': false,
        'recurrence': false,
        'similarity': false,
      },
    }
  },

  /** Always scroll to top */
  beforeMount() {
    window.scrollTo({top: 0})
  },

  /** Allow faster page rendering */
  mounted() {
    Object.keys(this.sections).forEach((section) => {
      setTimeout(() => {
        this.sections[section] = !this.sections[section]
      }, 1)
    })
  }
}
</script>

