<!--

  Component:    AnnotatePlot
  Description:  Component to control annotations

  Props:
   plotId:  Similarity plot id

-->

<template>
  <v-menu v-model="showOptions" content-class="rounded-xl options-menu larger" offset-y open-on-click width="450px"
          max-width="80vw" :open-on-hover="false" :close-on-content-click="false" rounded>

    <!-- Activator: button -->
    <template v-slot:activator="{ attrs, on }">
        <span v-bind="attrs" v-on="on">
          <btn-with-tooltip bottom color="primary" outlined hover-color="secondary" size="small"
                            icon="mdi-draw" text="Annotations"
                            :tip="showOptions?'':'Add/edit plot annotations'"
                            :click-handler="()=>showOptions=!showOptions"/>
        </span>
    </template>

    <!-- Actual menu with options-->
    <div @mouseleave="showOptions=true">
      <v-list color="bg" class="rounded-xl pb-6" rounded dense width="auto">
        <v-list-item dense class="align-baseline">
          <v-icon class="pr-3" color="tertiary">mdi-draw</v-icon>
          <v-list-item-content>

            <!-- Intro text -->
            <v-list-item-title class="tertiary--text">Plot annotations</v-list-item-title>
            <v-list-item-subtitle class="break-spaces">You can associate annotations with ranges of gene positions
            </v-list-item-subtitle>

            <!-- Body-->
            <div class="text-body-3 mt-3">
              <template v-if="!showRegularForm && !showCopyForm">

                <annotation-list-control :plot-id="plotId" editable/>

                <!-- Action buttons -->
                <btn-with-tooltip color='secondary' size="small" text="Add" icon="mdi-plus"
                                  :click-handler="()=>{showRegularForm=true}" content-class="ma-1"/>
                <btn-with-tooltip color='primary' size="small" text="Copy from another plot" icon="mdi-content-paste"
                                  :click-handler="()=>{showCopyForm=true}" content-class="ma-1"/>

              </template>

              <add-annotation-control v-if="showRegularForm" :plot-id="plotId" @close="showRegularForm=false;"/>
              <copy-annotation-control v-else-if="showCopyForm" :plot-id="plotId" @close="showCopyForm=false;"/>

            </div>
          </v-list-item-content>
        </v-list-item>
      </v-list>

    </div>
  </v-menu>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import AddAnnotationControl from "@/components/analysis/plots/controls/AddAnnotationControl.vue";
import CopyAnnotationControl from "@/components/analysis/plots/controls/CopyAnnotationControl.vue";
import AnnotationListControl from "@/components/analysis/plots/controls/AnnotationListControl.vue";

export default {
  name: "AnnotatePlot",
  components: {AnnotationListControl, CopyAnnotationControl, AddAnnotationControl, BtnWithTooltip},

  props: {
    /** Similarity plot id */
    plotId: {},
  },

  data() {
    return {
      /** Boolean visibility flag for the aggregation options menu */
      showOptions: false,
      /** Boolean visibility flag for copy-from option */
      showCopyForm: false,
      /** Boolean visibility flag for add-annotation option */
      showRegularForm: false,
    }
  },
}
</script>

