<!--

  Component:    AddAnnotationControl
  Description:  Component to add annotations

  Props:
   plotId:  Similarity plot id

  Events:
   close:  Go back event

-->

<template>
  <v-form @submit.prevent="">
    <!-- Category selector -->
    <v-select v-model="category" class="pt-0 text_bg--text" placeholder="Annotation category" :items="categories"
              color="secondary"/>

    <v-expand-transition hide-on-leave>
      <!-- Category annotation selector -->
      <v-autocomplete v-if="category" v-model="selectedAnnotations" multiple :items="annotationsOptions"
                      item-text="displayName" item-value="displayName" return-object color="secondary" deletable-chips
                      small-chips placeholder="Select annotations to be added" no-data-text="No matching annotations">
        <template v-slot:prepend-item>
          <!-- Select all option -->
          <v-list-item link @click="selectAllAnnotations">
            <v-list-item-action>
              <v-simple-checkbox :value="selectedAnnotations.length===annotationsOptions.length"/>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title v-if="selectedAnnotations.length!==annotationsOptions.length">Select all
              </v-list-item-title>
              <v-list-item-title v-else>Deselect all</v-list-item-title>

            </v-list-item-content>
          </v-list-item>
          <v-divider/>
        </template>
      </v-autocomplete>

    </v-expand-transition>

    <div class="d-flex mr-5 mb-2">
      <btn-with-tooltip :color="(!selectedAnnotations.length)?'secondary':'primary'" text="Back" icon="mdi-chevron-left"
                        size="small"
                        :click-handler="()=>$emit('close')"/>

      <v-spacer/>
      <btn-with-tooltip color="secondary" text="Add" icon="mdi-check" size="small"
                        :click-handler="manageAnnotationAddition" :disabled="!selectedAnnotations.length"/>
    </div>

  </v-form>
</template>

<script>

import proteinsJSON from '@/utils/annotations/proteins.json';
import {mapActions, mapGetters} from "vuex";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: "AddAnnotationControl",
  components: {BtnWithTooltip},

  props: {
    /** Similarity plot id */
    plotId: {},
  },

  data() {
    return {
      /** Selected category or null */
      category: null,

      /** Possible categories */
      categories: [
        {text: 'Proteins annotations', value: 'prot'}
      ],

      /** Possible annotation options for the selected category */
      annotationsOptions: [],

      /** Selected annotation options to be added */
      selectedAnnotations: [],
    }
  },

  computed: {
    ...mapGetters(['getSimilarityAnnotations'])
  },

  watch: {
    /** On category selection, fetch current selection and items*/
    category(newVal) {
      if (!newVal) return

      // Fetch options
      switch (newVal) {
        case 'prot':
          this.annotationsOptions = Object.values(proteinsJSON)
          break
      }

      // Fetch prev selection, all if none, otherwise empty array (avoid misleading delete behavior).
      const prevSelection = this.getSimilarityAnnotations[this.plotId].filter(({category}) => category === newVal)
      this.selectedAnnotations = prevSelection.length > 0 ? [] : [...this.annotationsOptions]
    },
  },

  methods: {
    ...mapActions(['addAnnotations']),

    /**
     * Manage select all / deselect all option
     */
    selectAllAnnotations() {
      if (this.selectedAnnotations.length === this.annotationsOptions.length) {
        this.selectedAnnotations = [] // deselect all
      } else {
        this.selectedAnnotations = [...this.annotationsOptions] //select all
      }
    },

    /**
     * Actually add annotations to the current plot and hide panel
     */
    manageAnnotationAddition() {
      this.addAnnotations({
        plotId: this.plotId,
        annotations: this.selectedAnnotations.map((item) => ({...item, category: this.category}))
      }).then(() => this.$emit('close'))
    }

  }
}
</script>

