/**
 *
 *  STORE ACTIONS METHODS
 *
 *  Usage:        dispatch('actionName',payload)
 *  Definition:   actionName({commit,dispatch,getters,state},payload){}
 *
 **/

export const actions = {

    /**
     * Clear all the analysis and set current analysis to null
     */
    clearHistory({commit, state}) {
        return new Promise(resolve => {
            commit('setCurrentAnalysis', null)
            Object.keys(state.analyses).forEach(id => commit('removeAnalysis', id))
            resolve()
        })
    },

    /**
     * Add a new analysis that differs from the current only on params configs
     * @param commit
     * @param state
     * @param getters
     * @param data      Data of new analysis
     * @param params    Params config
     */
    addGroupAnalysis({commit, state, getters}, {data, params}) {
        // Generate a fake analysis attempt
        const {serverId, title, probability, isRecombinant, muts} = getters.getCurrentAnalysis
        commit('addAnalysisAttempt', {id: serverId, title, probability, isRecombinant, muts, params})

        // Actually add analysis:   this expects serverId, title, seq, probability,
        //                          muts, bins and penalty to be in analysis attempt
        commit('addAnalysis', data)
    },

    /**
     * Add annotations to the specified similarity plot of the current analysis
     * @param commit
     * @param getters
     * @param plotId        Id of the similarity plot
     * @param annotations   Annotations to be added, if not existing
     */
    addAnnotations({commit, getters}, {plotId, annotations}) {
        return new Promise(resolve => {
            const existingAnnotations = getters.getSimilarityAnnotations[plotId].map(({displayName}) => displayName)
            const newAnnotations = annotations.filter(({displayName}) => !existingAnnotations.includes(displayName))
            commit('addAnnotations', {plotId, annotations:newAnnotations})
            resolve()
        })
    }
}