/**
 *  VUEX STATE
 *  State variables for the vuex state
 */

import {version} from '../../package.json'

export const state = {

    /**
     * Object defining the performed analysis. Each obj has the following structure
     *  id: unique integer identifier (ex: 0)
     *  serverId: server analysis identifier (ex: 0)
     *  title: string title
     *  performedOn: full date of execution date (ex. 'Wed Dec 07 2022, 10:04')
     *  starred: boolean star status flag
     *  performedOn: full date of execution date (ex. 'Wed Dec 07 2022, 10:04')
     *  notes: 'string containing the notes' or null value if none
     *  muts: [] mutations in the sequence
     */
    analyses: {},

    /** Identifier of the currently shown analysis or null value */
    currentAnalysisId: null,

    /** Summary of the analysis attempt data. Object including:
     *  serverId: server analysis identifier (ex: 0)
     *  muts: [] mutations in the sequence
     *  isRecombinant: flag set to true iff the sequence is considered as recombinant
     *  probability: probability of recombination
     */
    analysisAttempt: null,

    /** Default analysis params TODO: update */
    defaultParams: {
        bins: 12,
        penalty: 56,
    },

    /** Reset flag, if set to true the storage is cleared */
    reset: false,

    /** Dark mode flag, if set to true dark mode is enabled */
    darkMode: undefined,
    /** Auto dark mode flag, if set to true dark mode is enabled automatically */
    autoDarkMode: true,

    /** Global loading flag */
    loading: false,

    /** Frontend app version*/
    version: version,
}