<!--

  Component:    OverviewStep
  Description:  Second step of the new analysis definition: overview of the preliminary analysis .

  Events:
   prev:   On prev step request

-->

<template>
  <v-container class="pt-10 definition-container">

    <v-row>
      <v-col class="text-h6 compact-h5">
        <div class="px-5 ">
          A quick <span class="font-weight-bold tertiary--text">overview</span> of your sequence before performing the analysis
        </div>
      </v-col>
    </v-row>
    <!-- Mutation list -->
    <v-row class="mt-8">
      <mutation-table/>
    </v-row>

    <v-row justify="center">
      <v-col cols="10">
        <v-divider/>
      </v-col>
    </v-row>

    <!-- Outcome -->
    <v-row justify="center" class="mt-3 mb-3 px-7">
      <v-col>
        <list-item :color="isRecombinant?'success':'error'" class="break-spaces" larger-title auto-height
                   :title="title" icon="mdi-merge" :subtitle="subtitle"/>
      </v-col>
    </v-row>

    <!-- Controls -->
    <v-row class='mt-10 mb-2' justify="center">
      <v-col cols="auto">
        <param-selector v-model="customParams"/>
      </v-col>
    </v-row>
    <v-row>
      <btn-with-tooltip icon="mdi-chevron-left" :color="isRecombinant?'primary':'secondary'"
                        :hover-color="isRecombinant?'error':'success'" bottom
                        :content-class="isRecombinant?'text_primary--text':'text_secondary--text'"
                        :text="isRecombinant?'Back':'Select another sequence'"
                        :click-handler="()=>$emit('prev')"/>
      <v-spacer/>
      <btn-with-tooltip icon="mdi-chevron-right" :color="isRecombinant?'secondary':'primary'"
                        :hover-color="isRecombinant?'success':'error'" bottom append-icon
                        :content-class="isRecombinant?'text_secondary--text':'text_primary--text'"
                        :text="isRecombinant?'Proceed':'Proceed anyway'" :click-handler="sendAnalysis"/>

    </v-row>

    <loading-sticker :is-loading="isLoading" :error="error"
                     :loading-messages="loadingMessages"/>

  </v-container>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import MutationTable from "@/components/form/MutationTable.vue";
import ListItem from "@/components/general/basic/ListItem.vue";
import {mapMutations, mapState} from "vuex";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import ParamSelector from "@/components/form/ParamSelector.vue";

export default {
  name: "OverviewStep",
  components: {ParamSelector, LoadingSticker, ListItem, MutationTable, BtnWithTooltip},

  data() {
    return {
      /** Params to be used in the analysis */
      customParams: {},

      /** Loading messages */
      loadingMessages: [
        {text: 'Analyzing sequence data', time: 3000},
        {text: 'This may take some time', time: 8000},
        {text: 'Recombination hunting', time: 20000},
        {text: 'Evaluating other potential recombinations', time: 30000},
        {text: 'Almost done! Hang in there', time: 50000}
      ],

      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    ...mapState(['analysisAttempt']),

    /** Flag set to true if the sequence is recombinant */
    isRecombinant() {
      return this.analysisAttempt.isRecombinant
    },

    /** Recombination probability */
    probability() {
      return this.analysisAttempt.probability
    },

    /** Outcome title */
    title() {
      return this.isRecombinant
          ? 'It looks like this sequence is the result of recombination.'
          : 'It looks like this sequence is not the result of recombination.'
    },

    /** Outcome subtitle */
    subtitle() {
      return this.isRecombinant
          ? '<b>There is ' + this.probability*100 + '% probability that the sequence is recombinant</b><br/>' +
          'The sequence was analyzed and evidence was found that might suggest that ' +
          'the sequence is the result of recombination between SARS-CoV-2 variants. ' +
          'Proceed with the analysis to investigate further.'
          : '<b>There is ' + this.probability*100 + '% probability that the sequence is recombinant</b><br/>' +
          'The sequence was analyzed and no evidence was found to suggest that ' +
          'the sequence was the result of recombination between SARS-CoV-2 variants.'
    },
  },

  watch:{
    customParams(newVal){
      this.updateAnalysisAttempt(newVal)
    }
  },

  methods: {
    ...mapMutations(['addAnalysis','updateAnalysisAttempt']),

    /**
     * Triggers the analysis request to the server
     */
    sendAnalysis() {
      this.isLoading = true
      this.error = undefined
      const url = '/analyseSequence' // TODO: add API url

      // TODO remove lines below
      const data = {
        lineagePairs: [['Delta', 'BA.1'], ['BA.1', 'BA.2'], ['BA.1', 'BA.3'], ['BA.5', 'BA.3'], ['BA.5', 'BA.5'], ['BA.1.11', 'BA.1.1.1']],
        pairScores: [0.6, 0.54, 0.50, 0.45, 0.43, 0.4]
      }
      this.addAnalysis(data)
      // TODO remove lines above

      /*
      // TODO: correctly bind params
      const params = {
        id: this.analysisAttempt.serverId,
        bins: this.analysisAttempt.bins,
        penalty: this.analysisAttempt.penalty,
      }

      this.$axios
          .get(url, {params})
          .then(({data}) => {
            // Save results in store and change panel
            this.addAnalysis(data)
          })
          .catch((e) => this.error = e)
          .finally(() => this.isLoading = false)
       */
    },
  },

  beforeMount() {
    this.customParams = {
      bins: this.analysisAttempt.bins,
      penalty: this.analysisAttempt.penalty
    }
  }
}
</script>

