<template>
  <v-row class="px-5 mt-8">
    <v-container>
      <!-- Intro text -->
      <v-row>
        <v-col>
          <div class="text-h5 font-weight-black tertiary--text pb-3">
            Featured Recombinations
          </div>

          <v-sheet class="mx-auto rounded-xl" color="transparent" flat min-width="100%" max-width="fit-content">
            <v-slide-group class="px-0" show-arrows>
              <v-slide-item v-for="(example,idx) in examples" :key="idx">
                <!-- Example card element -->
                <v-card class="mr-3 mt-0 pa-5 pb-2 rounded-xl text-left tertiary-border" flat max-width="50vw" width="340px"
                        color="bg">
                  <!-- Example title and intro -->
                  <div class="example-title text_bg--text" @click="show(idx)">
                    {{ example.title }}
                  </div>
                  <div class="example-intro text_bg--text pt-1 pb-3">
                    {{ example.intro }}
                  </div>

                  <div class="mb-2">
                    <v-chip v-for="label in example.labels" :key="label" x-small :color="getLabelColor(label).color"
                            :light="!getLabelColor(label).isDark" small :dark="getLabelColor(label).isDark"
                            class="text-uppercase mr-1 mb-1">
                      {{ label }}
                    </v-chip>
                  </div>

                  <!-- Example actions -->
                  <v-btn class="mr-2 mb-2 text_quaternary--text" rounded x-small elevation="0" @click="show(idx)" color="quaternary">
                    <v-icon color="text_quaternary" left x-small>mdi-plus</v-icon>
                    Details
                  </v-btn>
                  <v-btn class="mb-2 text_quaternary--text" rounded x-small elevation="0" @click="open(idx)" color="quaternary">
                    <v-icon color="text_quaternary" left x-small>mdi-lead-pencil</v-icon>
                    Open
                  </v-btn>
                </v-card>
              </v-slide-item>
            </v-slide-group>

            <!-- EXAMPLE DIALOG  -->
            <v-dialog v-model="showDialog" max-width="900" transition="dialog-bottom-transition">
              <v-card class="rounded-xl">
                <!-- Dialog toolbar -->
                <v-toolbar class="dialog-title pl-2" color="transparent" flat>
                  <v-spacer></v-spacer>
                  <v-btn icon @click="showDialog=false">
                    <v-icon>mdi-close-thick</v-icon>
                  </v-btn>
                </v-toolbar>

                <v-card-text class='text-s-center dialog-text'>
                  <v-container>
                    <v-row justify="center" no-gutters>

                      <!-- Description -->
                      <v-col class="text-left" cols="9">
                        <div class="text-right">
                          <v-chip class="mt-0 mb-2 hidden-xs text-uppercase text_tertiary--text" color="tertiary" small>
                            featured example
                          </v-chip>
                        </div>

                        <h3 class="font-weight-bold text-h5 tertiary--text">{{ examples[opened].title }}</h3>
                        <div class="my-2">
                          <v-chip v-for="label in examples[opened].labels" :key="label" x-small :color="getLabelColor(label).color"
                                  :light="!getLabelColor(label).isDark" small :dark="getLabelColor(label).isDark"
                                  class="text-uppercase mr-1 mb-1">
                            {{ label }}
                          </v-chip>
                        </div>

                        <p class="text_bg--text" v-html="examples[opened].body"/>

                        <v-btn class="my-3 text_tertiary--text" color="tertiary" elevation="0" rounded small @click="open(opened)">
                          <v-icon left small>mdi-lead-pencil</v-icon>
                          Open example in Recombin<span class="font-weight-bold quaternary--text">Hunt</span>
                        </v-btn>
                      </v-col>

                    </v-row>
                  </v-container>
                </v-card-text>
              </v-card>
            </v-dialog>
          </v-sheet>
        </v-col>

      </v-row>
    </v-container>

    <loading-sticker :is-loading="isLoading" :error="error"
                     :loading-messages="[{text:'Analyzing sequence data',time:3000},{text:'This may take some time',time:6000},{text:'Almost done! Hang in there',time:9000}]"/>


  </v-row>
</template>

<script>
import {examples} from "@/store/examples";
import {getRandomColor} from "@/utils/colorService";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import {mapMutations} from "vuex";

export default {
  name: "QuickStart",
  components: {LoadingSticker},

  data() {
    return {
      /** Boolean flag set to true iff the example dialog is shown */
      showDialog: false,

      /** Identifier for the currently opened example */
      opened: 0,

      /** Object storing label colors*/
      labelColors: {},

      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    /** Examples */
    examples() {
      return examples
    }
  },

  methods: {
    ...mapMutations(['addAnalysisAttempt']),

    /** Get a random color to be associated with the label */
    getLabelColor(label) {
      if (!this.labelColors[label])
        this.labelColors[label] = getRandomColor()
      return this.labelColors[label]
    },

    /**
     * Show the dialog related to a given example idx
     * @param idx   Example index
     */
    show(idx) {
      this.showDialog = true
      this.opened = idx
    },

    /**
     * Run the example with a given example idx
     * @param idx   Example index
     */
    open(idx) {
      this.showDialog = false

      const panelTitle = this.examples[idx].panelTitle
      const muts = this.examples[idx].muts
      this.sendList(panelTitle,muts)
    },

    /**
     * Send mutations list to the server
     */
    sendList(panelTitle,muts) {
      this.isLoading = true
      this.error = undefined
      const url = '/sendMutationList' // TODO: findSequence API

      // TODO remove lines below
      const data = {
        id: 1,
        title: panelTitle,
        probability: 0.8,
        isRecombinant: true,
        muts: muts,
      }
      this.addAnalysisAttempt(data)
      this.$emit('next')
      // TODO remove lines above
      /*
      this.$axios
          .post(url, {mutations: muts})
          .then(({data}) => {
            // Set title as panel title
            data.title=panelTitle

            // Save results in store
            this.addAnalysisAttempt(data)
            this.$emit('next')
          })
          .catch((e) => this.error = e)
          .finally(() => this.isLoading = false)
       */

    },
  }
}
</script>

<style scoped>
.example-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
}

.example-intro {
  font-weight: 300;
  font-size: 14px;
  line-height: 14px;
}
