/**
 *
 *  STORE MUTATIONS METHODS
 *
 *  Usage:        commit('mutationName',payload)
 *  Definition:   mutationName(state,payload){}
 *
 **/

import Vue from "vue";
import {getRandomColor} from "@/utils/colorService";

export const mutations = {


    /** ********************************************* **/
    /**        MANAGERS for ANALYSIS ATTEMPT          **/
    /** ********************************************* **/

    /**
     * Initialize analysis attempt
     * @param state
     * @param id                Server assigned id
     * @param title             Title for the analysis
     * @param probability       Recombination probability
     * @param isRecombinant     Recombination outcome
     * @param muts              Sequence mutations
     * @param params            Analysis params
     */
    addAnalysisAttempt(state, {id, title = undefined, probability, isRecombinant, muts, params = undefined}) {
        // Set title as <mut_count>_<date> if not provided
        if (!title) {
            const date = new Date()
            title = muts.length + 'muts ' + date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
        }

        Vue.set(state, 'analysisAttempt', {
            serverId: id,
            title,
            probability,
            isRecombinant,
            muts,
            bins: params ? params.bins : state.defaultParams.bins,
            penalty: params ? params.penalty : state.defaultParams.penalty,
        })
    },

    /**
     * Update analysis params
     * @param state
     * @param bins              Num of bins
     * @param penalty           Penalty factor
     */
    updateAnalysisAttempt(state, {bins, penalty}) {
        Vue.set(state.analysisAttempt, 'bins',bins)
        Vue.set(state.analysisAttempt, 'penalty',penalty)
    },

    /** ********************************************* **/
    /**          MANAGERS for ANALYSIS DATA           **/
    /** ********************************************* **/

    /**
     * Add a new analysis
     * @param state
     * @param data   Data object storing recombination results
     */
    addAnalysis(state, {lineagePairs, pairScores}) {
        // Assign id to the analysis
        const pastKeys = Object.keys(state.analyses).map(x => Number(x))
        const assignedId = pastKeys.length > 0 ? (Math.max(...pastKeys) + 1) : 0

        //Assign colors to pairs
        const colors = []
        while (lineagePairs.length > 0 && colors.length !== lineagePairs.length) {
            const randomColor = getRandomColor()
            if (!colors.includes(randomColor))
                colors.push(randomColor)
        }

        const {serverId, title, probability, isRecombinant, muts, bins, penalty} = state.analysisAttempt
        Vue.set(state.analyses, assignedId, {
            id: assignedId,
            serverId,
            starred: false,
            performedOn: new Date().toDateString() + ", " + new Date().toTimeString().slice(0, 5),
            title,
            classification: {probability, isRecombinant},
            params: {bins, penalty},
            muts,
            recurrence: {lineagePairs, pairScores, colors},
            similarities: [],
            notes: null,
        })

        // Show the newly created analysis
        state.currentAnalysisId = assignedId
    },

    /**
     * Remove an analysis given its id
     * @param state
     * @param id        Analysis id
     */
    removeAnalysis(state, id) {
        if (id === state.currentAnalysisId)
            state.currentAnalysisId = null

        Vue.delete(state.analyses, id)
    },

    /**
     * Sets the current analysis id
     * @param state
     * @param id    Id to be set
     */
    setCurrentAnalysis(state, id) {
        state.currentAnalysisId = id
    },

    /**
     * Set analysis title
     * @param state
     * @param title     New title
     */
    setTitle(state, title) {
        Vue.set(state.analyses[state.currentAnalysisId], 'title', title)
    },

    /**
     * Sets the starred flag of an analysis
     * @param state
     * @param id        Analysis id
     * @param starred   Boolean value for the starred flag
     */
    setStarredAnalysis(state, {id, starred = true}) {
        state.analyses[id].starred = starred
    },

    /**
     * Set the notes associated with the current analysis
     * @param state
     * @param note  The notes
     */
    setNotes(state, note) {
        Vue.set(state.analyses[state.currentAnalysisId], 'notes', note)
    },


    /** ********************************************* **/
    /**         MANAGERS for SIMILARITY DATA          **/
    /** ********************************************* **/

    /**
     * Add data of a similarity plot
     * @param state
     * @param data  Similarity data
     */
    addSimilarityData(state, {pair, color, bins, j1sx, j2dx, c, coordinates}) {
        const currAnalysis = state.analyses[state.currentAnalysisId]
        const similarities = currAnalysis.similarities
        const pastKeys = similarities.map(({id}) => Number(id))
        const assignedId = pastKeys.length > 0 ? (Math.max(...pastKeys) + 1) : 0

        Vue.set(currAnalysis, 'similarities', [...similarities, {
            id: assignedId,
            pair,
            color,
            bins,
            annotations: [],
            type: 'std',
            j1sx: [j1sx],
            j2dx: [j2dx],
            c: [c],
            coordinates: [coordinates]
        }])
    },

    /**
     * Add a similarity comparison plot by merging two existing plots
     * @param state
     * @param ids   Ids for the plots to be merged
     */
    addSimilarityCmp(state, ids) {
        const currAnalysis = state.analyses[state.currentAnalysisId]
        const similarities = currAnalysis.similarities
        const pastKeys = similarities.map(({id}) => Number(id))
        const assignedId = pastKeys.length > 0 ? (Math.max(...pastKeys) + 1) : 0

        const [s1, s2] = similarities.filter(({id}) => ids.includes(id))

        Vue.set(currAnalysis, 'similarities', [...similarities, {
            id: assignedId,
            pair: [...s1.pair, ...s2.pair],
            color: getRandomColor(),
            bins: undefined,
            annotations: [],
            type: 'cmp',
            j1sx: [...s1.j1sx, ...s2.j1sx],
            j2dx: [...s1.j2dx, ...s2.j2dx],
            c: [...s1.c, ...s2.c],
            coordinates: [...s1.coordinates, ...s2.coordinates]
        }])
    },

    /**
     * Remove data of a similarity plot
     * @param state
     * @param id  Identifier
     */
    removeSimilarityData(state, id) {
        const currAnalysis = state.analyses[state.currentAnalysisId]
        const updatedList = currAnalysis.similarities.filter(({id: elId}) => elId !== id)
        Vue.set(currAnalysis, 'similarities', updatedList)
    },

    /**
     * Update data of a similarity plots
     * @param state
     * @param newVal  Similarity plots data
     */
    updateSimilarityData(state, newVal) {
        Vue.set(state.analyses[state.currentAnalysisId], 'similarities', newVal)
    },

    /**
     * Add annotations to the specified similarity plot of the current analysis
     * @param state
     * @param plotId        Id of the similarity plot
     * @param annotations   Annotations array
     */
    addAnnotations(state, {plotId, annotations}) {
        const similarityPlot = state.analyses[state.currentAnalysisId].similarities.find(({id}) => id === plotId)
        if (!similarityPlot) return

        const prevAnnotations = similarityPlot.annotations
        const newAnnotations = [...prevAnnotations, ...annotations]
        Vue.set(similarityPlot, 'annotations', newAnnotations)
    },

    /**
     * Remove an annotation from the specified similarity plot of the current analysis
     * @param state
     * @param plotId       Id of the similarity plot
     * @param annotation   Annotation name
     */
    removeAnnotation(state, {plotId, annotation}) {
        const similarityPlot = state.analyses[state.currentAnalysisId].similarities.find(({id}) => id === plotId)
        if (!similarityPlot) return

        const prevAnnotations = similarityPlot.annotations
        const newAnnotations = prevAnnotations.filter(({displayName}) => displayName !== annotation)
        Vue.set(similarityPlot, 'annotations', newAnnotations)
    },

    /**
     * Remove all the annotation from the specified similarity plot of the current analysis
     * @param state
     * @param plotId       Id of the similarity plot
     */
    clearAnnotations(state, plotId) {
        const similarityPlot = state.analyses[state.currentAnalysisId].similarities.find(({id}) => id === plotId)
        if (!similarityPlot) return

        Vue.set(similarityPlot, 'annotations', [])
    },


    /** ********************************************* **/
    /**                 APP STATE MANAGERS            **/
    /** ********************************************* **/

    /**
     * Set the flag to reset the state. Requires page reload.
     */
    resetState(state) {
        state.reset = true
    },
    /**
     * Set the global loading state
     * @param state
     * @param newVal    The new value
     */
    setLoading(state, newVal) {
        state.loading = newVal
    },

    /**
     * General purpose setter
     * @param state
     * @param name      Name of the property
     * @param newVal    Value for the property
     */
    setState(state, {name, newVal}) {
        Vue.set(state, name, newVal)
    }
}