<!--

  Component:    PlotInfoBtn
  Description:  Component to show info about plot

  Props:
   label:  Activator label

  Slots:
   default:  Info slot

-->

<template>
  <v-menu v-model="showOptions" content-class="rounded-xl options-menu" offset-y open-on-click width="750px"
          max-width="80vw" max-height="70vh" :open-on-hover="false" :close-on-content-click="false" rounded z-index="14">

    <!-- Activator: button -->
    <template v-slot:activator="{ attrs, on }">
        <span v-bind="attrs" v-on="on">
          <btn-with-tooltip bottom color="primary" outlined hover-color="secondary" size="small"
                            icon="mdi-information-variant" :text="label"
                            :tip="showOptions?'':'Info about this plot'"
                            :click-handler="()=>showOptions=!showOptions"/>
        </span>
    </template>

    <!-- Actual menu with aggregation options-->
    <div @mouseleave="showOptions=false">
      <v-card width="auto" class="rounded-xl" color="bg" elevation="0">
        <v-card-actions class="mx-5 mt-2">
          <v-spacer/>
          <btn-with-tooltip color="secondary" text="Hide" icon="mdi-close" size="x-small"
                            :click-handler="()=>this.showOptions=false"/>

        </v-card-actions>

        <v-card-text>
          <slot></slot>
        </v-card-text>


      </v-card>
    </div>
  </v-menu>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: "PlotInfoBtn",
  components: {BtnWithTooltip},

  props: {
    /** Value of the aggregation mode */
    value: {},

    /** Activator label */
    label: {default: 'About this plot'},
  },

  data() {
    return {
      /** Boolean visibility flag for the aggregation options menu */
      showOptions: false,

      /** Limit value */
      limit: -1,

      limitRules: [
        val => !!val || 'Required.',
        val => (Number.isInteger(Number(val)) && (Number(val) > 0 || Number(val) === -1)) || 'Must be a positive integer or -1.',
      ],

      /** Boolean flag set to true if the form is valid */
      validForm: null,
    }
  },

  methods: {
    /**
     * Change the limit
     */
    apply() {
      this.$emit('input', Number(this.limit))
      this.showOptions = false
    },

    /**
     * Apply no limit
     */
    noLimit() {
      this.limit = -1
      this.$emit('input', Number(-1))
      this.showOptions = false
    }
  },

  mounted() {
    this.limit = this.value
  }
}
</script>

