.options {
  padding-left: 35px;
  position: absolute;
  top: 0;
}

.options-intro {
  max-width: 170px;
  padding-top: 13px;
  margin-bottom: 10px;
  color: var(--v-text_var1-base);
  font-weight: normal;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.019em;
}

.options-intro .main {
  font-weight: bold;
}
</style>