<!--

  Component:    SimilarityPlotOptions
  Description:  Component to show additional options to the similarity plot

  Props:
   value:    Select value for cmpMode
   plotId:       Identifier for the similarity plot
   cmpMode:  Compare mode flag
   dragMode: Drag mode flag

-->

<template>
  <div>
    <template v-if="dragMode">
      <icon-with-tooltip tip="Remove analysis" color="secondary" hover-color="error" bottom icon="mdi-trash-can-outline"
                         :click-handler="deletePlot"/>
      <icon-with-tooltip tip="Drag to reorder" color="secondary" :size="30" bottom icon="mdi-drag"/>
    </template>
    <template v-if="cmpMode">
      <v-tooltip bottom allow-overflow z-index="999" :close-delay="0" max-width="400px" :open-delay="200">
        <template v-slot:activator="{ on, attrs }">
          <div v-on="on" v-bind="attrs">
            <v-checkbox v-model="checked" light readonly/>
          </div>
        </template>
        {{ checked ? 'Deselect' : 'Select to compare' }}
      </v-tooltip>
    </template>


  </div>
</template>

<script>
import IconWithTooltip from "@/components/general/basic/IconWithTooltip.vue";
import {mapMutations} from "vuex";

export default {
  name: "SimilarityPlotOptions",
  components: {IconWithTooltip,},

  props: {
    /** Identifier for the similarity plot */
    plotId: {},

    /** Select value for cmpMode*/
    value: {},

    /** Drag mode flag */
    dragMode: Boolean,

    /** Compare mode flag */
    cmpMode: Boolean,
  },

  data() {
    return {}
  },

  computed: {
    /** Selection status for compare mode */
    checked: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    }
  },

  methods:{
    ...mapMutations(['removeSimilarityData']),

    deletePlot(){
      this.removeSimilarityData(this.plotId)
    }
  }
}
</script>

