<template>
  <dialog-element v-model="showDialog" width="800px" title="New similarity analysis" icon="mdi-view-grid-plus-outline"
                  :with-activator="mode==='standalone'">

    <template v-slot:activator v-if="mode==='standalone'">
      <btn-with-tooltip text="Generate similarity analysis" icon="mdi-view-grid-plus-outline" bottom
                        tip="Generate new similarity analysis between candidate lineage pairs"
                        color="secondary" outlined :click-handler="()=>showDialog=true"/>
    </template>

    <template v-slot:body>
      <v-row class="px-5" dense no-gutters>
        <v-col>
          <v-alert type="info" dense color="text_bg" outlined class="rounded-xl pt-3 bg_var2"
                   icon="mdi-information-variant">
            <div class="text-body-1 font-weight-bold tertiary--text">What is a similarity plot?</div>
            <p>Brief description of what a similarity plot is and why one should be interested. NO LONGER THAN THIS.
              Lorem ipsum dolor sit amet. Quo provident doloribus ut rerum corporis tempora aut consequatur galisum aut
              accusamus tempora aut voluptas ratione.</p><!-- TODO update text-->
          </v-alert>
        </v-col>
      </v-row>

      <!-- Header -->
      <v-row class="px-10 mb-5">
        <v-col>
          Customize the parameters for the similarity analysis
        </v-col>
      </v-row>

      <v-form v-model="validForm" @submit.prevent="sendAnalysis">
        <v-row justify="center">

          <!-- Pairs selector -->
          <v-col cols="10">
            <v-row>
              <select-element v-model="selectedPair" label="Lineage pair:" description="lineages to be compared"
                              hide-details placeholder="Select from list" no-clear :rules="rules.pair"
                              :items="getAllLineagesPairs" return-obj>
                <template v-slot:item="{item}">
                  <div class="d-flex width-100">
                    <v-icon :color="item.color.color">mdi-circle</v-icon>
                    <span class="ml-3">{{ item.text }}</span>
                  </div>
                </template>
                <template v-slot:selection="{item}">
                  <div class="d-flex align-center width-100">
                    <v-icon :color="item.color.color">mdi-circle</v-icon>
                    <span class="ml-3">{{ item.text }}</span>
                  </div>
                </template>
              </select-element>
            </v-row>
          </v-col>

          <!-- Number of bins -->
          <v-col cols="10">
            <v-row>
              <input-element v-model="bins" label="Number of bins:" description="brief description here"
                             placeholder="Required" no-clear :rules="rules.bins">
                <template v-slot:append-outer>
                  <icon-with-tooltip icon="mdi-information-outline" :tip="hints.bins" bottom no-link/>
                </template>
              </input-element>
            </v-row>
          </v-col>

        </v-row>
      </v-form>

      <!-- Loading/error management -->
      <loading-sticker :is-loading="isLoading" :error="error" :loading-messages="loadingMessages"/>

    </template>

    <template v-slot:actions>
      <v-spacer/>
      <btn-with-tooltip icon="mdi-check" :color="validForm?'secondary':'primary'"
                        bottom :disabled="!validForm"
                        :content-class="validForm?'text_secondary--text':'text_primary--text'"
                        append-icon
                        text="Generate analysis" :click-handler="sendAnalysis"/>
    </template>

  </dialog-element>
</template>

<script>
import DialogElement from "@/components/general/basic/Dialog.vue";
import IconWithTooltip from "@/components/general/basic/IconWithTooltip.vue";
import InputElement from "@/components/form/InputElement.vue";
import {mapGetters, mapMutations} from "vuex";
import SelectElement from "@/components/form/SelectElement.vue";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";

export default {
  name: "SimilarityAnalysisGenerator",
  components: {LoadingSticker, BtnWithTooltip, SelectElement, InputElement, IconWithTooltip, DialogElement},

  props: {
    /** Visibility flag */
    value: {required: true},

    /** Text of the preselected pair (eg BA.1 + BA.2) */
    preselectedPair: {},

    /** Mode of the generator. Either 'plot' or 'standalone' */
    mode: {default: 'plot'},

  },

  data() {
    return {
      /** Selected pair */
      selectedPair: null,

      /** Bins */
      bins: 0,

      /** Detailed params descriptions TODO: update params hints  */
      hints: {
        bins: 'This is a possibly long and detailed description of this field',
      },

      /** Validation rules TODO: update rules*/
      rules: {
        pair: [
          val => !!val || 'Required.',
        ],

        bins: [
          val => !!val || 'Required.',
          val => (Number.isInteger(Number(val)) && val > 0) || 'Must be a positive integer',
        ],
      },

      /** Boolean flag set to true if the form is valid */
      validForm: null,


      /** Loading messages */
      loadingMessages: [
        {text: 'Analyzing the similarity', time: 3000},
        {text: 'This may take some time', time: 8000},
        {text: 'Generating similarity plot', time: 15000},
        {text: 'Almost done! Hang in there', time: 20000}
      ],

      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    ...mapGetters(['getAllLineagesPairs', 'getCurrentAnalysis']),

    /** Show dialog */
    showDialog: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    },
  },

  watch: {
    showDialog(newVal) {
      if (newVal && this.preselectedPair)
        this.selectedPair = this.getAllLineagesPairs.find(({text})=>{
          return text===this.preselectedPair
        })
      this.bins = this.getCurrentAnalysis.params.bins
    }
  },

  methods: {
    ...mapMutations(['addSimilarityData']),

    /**
     * Triggers the analysis request to the server
     */
    sendAnalysis() {
      this.isLoading = true
      this.error = undefined
      const url = '/analyseSimilarity' // TODO: add API url

      // TODO remove lines below
      this.addSimilarityData({
        pair: this.selectedPair.value,
        color: this.selectedPair.color,
        bins: this.bins,
        j1sx: [...Array(50).keys()].map(()=>Math.random()+0.5),
        j2dx: [...Array(50).keys()].map(()=>Math.random()+0.5),
        c: [...Array(50).keys()].map(()=>Math.random()+0.5),
        coordinates: [...Array(50).keys()].map(x=>x*598.06)
      })
      this.showDialog = false
      this.isLoading = false
      // TODO remove lines above

      /*
      // TODO: correctly bind params
      const requestBody = {
        id: this.getCurrentAnalysis.serverId,
        lineages: this.selectedPair,
        bins: this.bins,
      }

      this.$axios
          .post(url, requestBody)
          .then(({data}) => {
            // Save results in store and change panel
            this.addSimilarityData({
              pair: this.selectedPair,
              color: this.selectedPairColor,
              bins: this.bins,
              j1sx: data.j1sx,
              j2dx: data.j2dx,
              c: data.c,
              coordinates: data.coordinates
            })

            this.showDialog = false
          })
          .catch((e) => this.error = e)
          .finally(() => this.isLoading = false)
      /**/
    },
  }
}
</script>

