/**
 *  SCREENSHOT SERVICE
 *  It provides screenshot features
 */
import {toPng} from "html-to-image";

export function takeScreenshot(document, section, fileName, fn) {
    return {
        name: 'Take a screenshot of this section',
        icon: {
            name: 'Download',
            title: 'Download',
            svg: `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M4,4H7L9,2H15L17,4H20A2,2 0 0,1 22,6V18A2,2 0 0,1 20,20H4A2,2 0 0,1 2,18V6A2,2 0 0,1 4,4M12,7A5,5 0 0,0 7,12A5,5 0 0,0 12,17A5,5 0 0,0 17,12A5,5 0 0,0 12,7M12,9A3,3 0 0,1 15,12A3,3 0 0,1 12,15A3,3 0 0,1 9,12A3,3 0 0,1 12,9Z" /></svg>`
        },
        click: () => {
            fn()
            setTimeout(() => {
                // Retrieve section to be printed
                const sectionToPrint = document.getElementById(section)
                const ratio = sectionToPrint.clientHeight / sectionToPrint.clientWidth
                const width = 2500
                const height = width * ratio
                const filter = (node) => {
                    const exclusionClasses = ['modebar-btn', 'exclude-from-screen'];
                    return !exclusionClasses.some((classname) => node.classList?.contains(classname));
                }

                toPng(sectionToPrint, {
                    canvasWidth: width,
                    canvasHeight: height,
                    filter: filter,
                    backgroundColor: '#FFFFFF'
                })
                    .then((dataUrl) => {
                        // Anchor element to download the file
                        const anchor = document.createElement('a')
                        anchor.download = fileName + '.png'
                        anchor.href = dataUrl
                        anchor.target='_blank'
                        document.body.appendChild(anchor)

                        // Simulate click and remove element
                        anchor.click()
                        document.body.removeChild(anchor)
                        fn()
                    });
            }, 1)
        }
    }
}