<!--

  Component:    DialogElement
  Description:  Params definition form

  Props:
   title:            Title
   icon:             Icon
   noActions:        Flag set to true to hide actions bar
   persistent:       Flag set to true to make dialog persistent
   withActivator:    Flag set to true to show activator slot
   value:            Object of the form {bins, penalty}

  Slots:
   activator:    Activator slot
   body:         Dialog body content slot
   actions:      Actions slot

-->

<template>
  <v-dialog v-model="showDialog" :width="width" :persistent="persistent">
    <template v-slot:activator="{ on, attrs }" v-if="withActivator">
     <slot name="activator"></slot>
    </template>

    <v-card>
      <v-card-title class="font-weight-bold primary text_primary--text">
        <v-icon left color="text_primary">{{icon}}</v-icon>
        {{title}}
      </v-card-title>

      <v-container class="mt-3">
        <slot name="body"></slot>

        <v-row v-if="!noActions" class="ma-5" >
          <slot name="actions"></slot>
        </v-row>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: "DialogElement",

  props: {
    /** Visibility flag */
    value: {required: true},

    /** Title */
    title: {required: true},

    /** Icon */
    icon: {required: true},

    /** Flag set to true to make dialog persistent*/
    persistent: Boolean,

    /** Flag set to true to hide actions bar */
    noActions: Boolean,

    /** Flag set to true to show activator slot */
    withActivator: Boolean,

    /** Width of the dialog */
    width: {default: 700},
  },

  computed: {

    /** Show dialog */
    showDialog: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    },

  },
}
</script>

