/**
 *
 *  STORE GETTERS METHODS
 *
 *  Usage:        getterName(payload)
 *  Definition:   getterName:(state,getters)=>(payload)=>{}
 *
 **/
import {getRandomColor} from "@/utils/colorService";

export const getters = {
    /**
     * Gets a summary of the past analysis
     */
    getAnalysesSummary: (state) => {
        // Returns a time-sorted summary for each analysis
        return Object.values(state.analyses)
            .map(({id, title, starred, performedOn, params}) => ({id, title, starred, performedOn, params}))
            .sort(({id1}, {id2}) => id1 - id2).reverse()
    },

    /** Gets the currently displayed analysis */
    getCurrentAnalysis: (state) => {
        return state.analyses[state.currentAnalysisId]
    },


    /**
     * Gets the data for the recurrence plot.
     */
    getRecurrenceData: (state, getters) => {
        if (state.currentAnalysisId === null) return undefined
        const {lineagePairs, pairScores, colors} = getters.getCurrentAnalysis.recurrence
        return lineagePairs.map((lineages, idx) => (
            {lineages, score: pairScores[idx], color: colors[idx]}
        ))
    },

    /**
     * Gets the data for the similarity plots.
     */
    getSimilarityData: (state, getters) => {
        if (state.currentAnalysisId === null) return undefined
        return getters.getCurrentAnalysis.similarities
    },

    /**
     * Gets all the lineages pairs of the current analysis
     */
    getAllLineagesPairs: (state, getters) => {
        if (state.currentAnalysisId === null) return undefined
        return getters.getRecurrenceData.map(({lineages, color}) => ({
            text: lineages.join(' + '),
            value: lineages,
            color
        }))
    },

    /**
     * Gets an object storing the annotations info for each similarity plot
     */
    getSimilarityAnnotations: (state, getters) => {
        if (state.currentAnalysisId === null) return undefined
        const info = {}
        getters.getCurrentAnalysis.similarities.forEach(({id, annotations}) => (info[id] = annotations))
        return  info
    },


}