<!--

  Component:    CopyAnnotationControl
  Description:  Component to copy the annotations from and existing plot

  Props:
   plotId:     Similarity plot id (destination)

-->

<template>
  <v-form @submit.prevent="">
    <div class="my-3 text-body-3">
      Select the plot from which to copy the annotations
    </div>
    <!-- Plot selector -->
    <v-select v-model="copyId" class="pt-0 text_bg--text" placeholder="Plot to copy from" :items="plotsData"
              color="secondary" attach dense>
      <template v-slot:no-data>
        <v-sheet color="transparent" max-width="300px" class="warning--text pa-3">Unfortunately, there is no other plot
          from which to copy annotations
        </v-sheet>
      </template>
      <template v-slot:item="{item,attrs,on}">
        <v-list-item dense v-on="on" v-bind="attrs">
          <v-list-item-content>
            <v-list-item-title>{{ item.text }}</v-list-item-title>
          </v-list-item-content>
          <v-list-item-action class="text-uppercase letter-spacing-1">
            <v-chip v-if="item.type==='std'" color="secondary" small>Bins: {{ item.bins }}</v-chip>
            <v-chip v-else color="primary" small>Comparison</v-chip>
          </v-list-item-action>
        </v-list-item>
      </template>
    </v-select>

    <!-- Annotation summary to be confirmed -->
    <v-expand-transition hide-on-leave>
      <annotation-list-control v-if="copyId!==null" :plot-id="copyId"/>
    </v-expand-transition>
    <div v-if="copyId!==null" class="text-body-4 text-center">
      Annotations will be added to the existing ones.
    </div>

    <!-- Actions -->
    <div class="d-flex mr-5 mt-6 mb-2">
      <btn-with-tooltip :color="(copyId===null)?'secondary':'primary'" text="Back" icon="mdi-chevron-left" size="small"
                        :click-handler="()=>$emit('close')"/>

      <v-spacer/>
      <btn-with-tooltip color="secondary" text="Copy" icon="mdi-check" size="small"
                        :click-handler="manageAnnotationCopy" :disabled="copyId===null"/>
    </div>

  </v-form>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import {mapActions, mapGetters, mapMutations} from "vuex";
import AnnotationListControl from "@/components/analysis/plots/controls/AnnotationListControl.vue";

export default {
  name: "CopyAnnotationControl",
  components: {AnnotationListControl, BtnWithTooltip},

  props: {
    /** Similarity plot id */
    plotId: {},
  },

  data() {
    return {

      /** Id of the plot to copy */
      copyId: null,

    }
  },

  computed: {
    ...mapGetters(['getSimilarityData', 'getSimilarityAnnotations']),

    /** Plots options: all current similarity plots but the current and those without annotations */
    plotsData() {
      return this.getSimilarityData
          .filter(({id, annotations}) => id !== this.plotId && annotations.length > 0)
          .map(({id, pair, bins, type}) => ({
            pair, bins, type, // additional data
            text: ([pair[0], pair[1]].join(' + ')) + (type !== 'std' ? (' vs ' + [pair[2], pair[3]].join(' + ')) : ''),
            value: id
          }))
    }
  },

  methods: {
    ...mapActions(['addAnnotations']),

    /**
     * Actually copy the annotations from the selected plot, then close the dialog
     */
    manageAnnotationCopy() {
      this.addAnnotations({plotId: this.plotId, annotations: this.getSimilarityAnnotations[this.copyId]})
          .then(() => this.$emit('close'))
    }
  }
}
</script>

