<!--

  Component:    SequenceDefinition
  Description:  Form to provide the sequence data

  Props:
   value:   Currently selected mode

-->

<template>
  <v-container >

    <v-scroll-x-reverse-transition hide-on-leave>
      <!-- INPUT MODE: Sequence uploader -->
      <v-row v-if="mode==='file'" class="my-8">
        <sequence-uploader v-model="fastaContent"/>
      </v-row>

      <!-- INPUT MODE: Find by id -->
      <v-row v-else-if="mode==='id'" class="my-8">
        <input-element v-model="sequenceId" label="Sequence identifier:"
                       description="GISAID or GenBank unique sequence identifier" no-clear
                       placeholder="For example GISAID_EXAMPLE or GENBANK_EXAMPLE"
                       hint="We will retrieve the sequence from the database for you."
        />
      </v-row>

      <!-- INPUT MODE: Mutation list -->
      <v-row v-else-if="mode==='list'" class="my-8">
        <mutation-list-uploader v-model="mutationsList"/>
      </v-row>
    </v-scroll-x-reverse-transition>

    <!-- Controls -->
    <v-scroll-y-reverse-transition hide-on-leave>
      <v-row v-if="mode!==null" class='my-4'>
        <btn-with-tooltip icon="mdi-chevron-left" color="primary"
                          hover-color="error" bottom
                          content-class="text_primary--text"
                          text="Back" :click-handler="()=>mode=null"/>
        <v-spacer/>
        <btn-with-tooltip :disabled="formError" icon="mdi-magnify" text="Analyse sequence" color='tertiary' append-icon
                          hover-color="success" content-class="text_tertiary--text" :click-handler="sendAnalysis"/>

      </v-row>
    </v-scroll-y-reverse-transition>

    <loading-sticker :is-loading="isLoading" :error="error"
                     :error-messages="errorMessages"
                     :loading-messages="[{text:'Analyzing sequence data',time:3000},{text:'This may take some time',time:6000},{text:'Almost done! Hang in there',time:9000}]"/>

  </v-container>
</template>

<script>
import SequenceUploader from "@/components/form/SequenceUploader.vue";
import {mapMutations} from "vuex";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import InputElement from "@/components/form/InputElement.vue";
import MutationListUploader from "@/components/form/MutationListUploader.vue";

export default {
  name: "SequenceDefinition",
  components: {MutationListUploader, InputElement, BtnWithTooltip, LoadingSticker, SequenceUploader},

  props: {
    /** Currently selected mode */
    value: {}
  },

  data() {
    return {
      /** Content of the file or null if none */
      fastaContent: null,

      /** Id of the sequence to be found */
      sequenceId: null,

      /** List of mutations */
      mutationsList: [],

      /** Custom error messages */
      errorMessages: {},
      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  watch: {
    fastaContent() {
      if (!this.formError) {
        this.sendAnalysis() // Auto send
      }
    },

    mode() {
      // Reset
      this.sequenceId = null
      this.mutationsList = []
      this.fastaContent = null
    }
  },

  computed: {

    /** Currently selected mode or null */
    mode: {
      set(newValue) {
        this.$emit('input', newValue)
      },
      get() {
        return this.value
      }
    },

    /** Form error flag: true if the form cannot be sent */
    formError() {
      return !this.fastaContent && !this.mutationsList.length && !this.sequenceId?.trim()
    }
  },

  methods: {
    ...mapMutations(['addAnalysisAttempt']),

    /**
     * Send sequence file to the server
     */
    sendFile() {
      this.isLoading = true
      this.error = undefined
      const url = '/uploadSequence'  // TODO uploadSequence API

      // TODO remove lines below
      const data = {
        id: 1,
        title: this.fastaContent.slice(this.fastaContent[0] === '>' ? 1 : 0, this.fastaContent.indexOf('\n')),
        probability: 0.8,
        isRecombinant: true,
        muts: [
          'ABCD1', 'ABCE2', 'ABCF3', 'ABCD4', 'EFGH5', 'EFGL6', 'EFGM7', 'EFGN8',
          'ABCD9', 'ABCE10', 'ABCF11', 'ABCD12', 'EFGH13', 'EFGL14', 'EFGM15', 'EFGN16',
          'SABCD9', 'SABCE10', 'ABSCF11', 'ASBCD12', 'SEFGH13', 'EFSGL14', 'EFGSM15', 'EFSGN16']
      }
      this.addAnalysisAttempt(data)
      this.$emit('next')
      // TODO remove lines above

      /*
      this.$axios
          .post(url, {fileContent: this.fastaContent})
          .then(({data}) => {
            // Set title as sequence ID
            data.title=this.fastaContent.slice(this.fastaContent[0] === '>' ? 1 : 0, this.fastaContent.indexOf('\n'))

            // Save results in store
            this.addAnalysisAttempt(data)
            this.$emit('next')
          })
          .catch((e) => {
            this.error = e
            this.errorMessages={
              400: {
                title: "Invalid FASTA file",
                text: "The specified .FASTA file has problems. Please try another file."
              }
            }
           })
          .finally(() => this.isLoading = false)
       */
    },

    /**
     * Send mutations list to the server
     */
    sendList() {
      this.isLoading = true
      this.error = undefined
      const url = '/sendMutationList' // TODO: findSequence API

      // TODO remove lines below
      const data = {
        id: 1,
        probability: 0.8,
        isRecombinant: true,
        muts: [
          'ABCD1', 'ABCE2', 'ABCF3', 'ABCD4', 'EFGH5', 'EFGL6', 'EFGM7', 'EFGN8',
          'ABCD9', 'ABCE10', 'ABCF11', 'ABCD12', 'EFGH13', 'EFGL14', 'EFGM15', 'EFGN16',
          'SABCD9', 'SABCE10', 'ABSCF11', 'ASBCD12', 'SEFGH13', 'EFSGL14', 'EFGSM15', 'EFSGN16']
      }
      this.addAnalysisAttempt(data)
      this.$emit('next')
      // TODO remove lines above
      /*
      this.$axios
          .post(url, {mutations: this.mutationsList})
          .then(({data}) => {
            // Save results in store
            this.addAnalysisAttempt(data)
            this.$emit('next')
          })
          .catch((e) => {
            this.error = e
            this.errorMessages={
              400: {
                title: "Invalid mutation list",
                text: "The list of mutations contains one or more invalid mutations. " +
                    "Please make sure that the mutations are correctly defined."
              }
            }
          })
          .finally(() => this.isLoading = false)
       */

    },

    /**
     * Send sequence id to the server
     */
    sendId() {
      this.isLoading = true
      this.error = undefined
      const url = '/findSequence' // TODO: findSequence API

      // TODO remove lines below
      const data = {
        id: 1,
        title: this.sequenceId,
        probability: 0.8,
        isRecombinant: true,
        muts: [
          'ABCD1', 'ABCE2', 'ABCF3', 'ABCD4', 'EFGH5', 'EFGL6', 'EFGM7', 'EFGN8',
          'ABCD9', 'ABCE10', 'ABCF11', 'ABCD12', 'EFGH13', 'EFGL14', 'EFGM15', 'EFGN16',
          'SABCD9', 'SABCE10', 'ABSCF11', 'ASBCD12', 'SEFGH13', 'EFSGL14', 'EFGSM15', 'EFSGN16']
      }
      this.addAnalysisAttempt(data)
      this.$emit('next')
      // TODO remove lines above
      /*
      this.$axios
          .post(url, {sequenceId: this.sequenceId.trim()})
          .then(({data}) => {
            // Set title as sequence ID
            data.title=this.sequenceId

            // Save results in store
            this.addAnalysisAttempt(data)
            this.$emit('next')
          })
          .catch((e) => {
            this.error = e
            this.errorMessages = {
              400: {
                title: "Invalid ID or unsupported database",
                text: "The sequence identifier you provided does not exist or is not supported. " +
                    "The supported databases are GISAID and GenBank."
              },
              404: {
                title: "Sequence not found",
                text: "No sequence matching the provided ID  was found in the GISAID and GenBank databases." +
                    "If your sequence belongs to these databases, it may be that it is not yet available.  "
              }
            }
          })
          .finally(() => this.isLoading = false)
      */
    },


    /**
     * Triggers the appropriate analysis request to the server
     */
    sendAnalysis() {
      switch (this.mode) {
        case 'file':
          this.sendFile()
          break;
        case 'list':
          this.sendList()
          break;
        case 'id':
          this.sendId()
      }
    }

  },
}
</script>

