<!--

  Component:    ParamSelector
  Description:  Params definition form

  Props:
   mode:    Mode of the activator button. Either 'new' or 'info'
   value:   Object of the form {bins, penalty}

  Event:
   configured:   On apply click

-->

<template>
  <v-dialog v-model="showDialog" width="700" persistent>
    <template v-slot:activator="{ on, attrs }">
      <btn-with-tooltip v-if="mode==='new'" text="Customize analysis parameters" icon="mdi-tune" bottom size="x-small"
                        tip="Customize values for number of bins and penalty factor" color="text_bg"
                        content-class="bg--text"
                        :click-handler="()=>showDialog=true"/>
      <btn-with-tooltip v-else text="Edit" icon="mdi-pencil" bottom size="x-small" outlined
                        tip="Generate a new analysis with a new parameter configuration" append-icon
                        :click-handler="()=>showDialog=true"/>
    </template>

    <v-card>
      <v-card-title class="font-weight-bold primary text_primary--text">
        <v-icon left color="text_primary">mdi-tune</v-icon>
        Analysis parameters
      </v-card-title>

      <v-container class="mt-3">
        <!-- Header -->
        <v-row class="px-5">
          <v-col>
            Define here your preferred analysis parameters or use the default configuration
          </v-col>
        </v-row>

        <v-form v-model="validForm" @submit.prevent="">
          <v-row justify="center">

            <!-- Number of bins -->
            <v-col cols="10">
              <v-row>
                <input-element v-model="bins" label="Number of bins:" description="brief description here"
                               placeholder="Required" @clear="clear('bins')" :default-value="defaultParams.bins"
                               :rules="rules.bins">
                  <template v-slot:append-outer>
                    <icon-with-tooltip icon="mdi-information-outline" :tip="hints.bins" bottom no-link/>
                  </template>
                </input-element>
              </v-row>
            </v-col>

            <!-- Penalty factor -->
            <v-col cols="10">
              <v-row>
                <input-element v-model="penalty" label="Penalty factor:"
                               description="how much to penalize similar lineages"
                               placeholder="Required" @clear="clear('penalty')" :default-value="defaultParams.penalty"
                               :rules="rules.penalty">
                  <template v-slot:append-outer>
                    <icon-with-tooltip icon="mdi-information-outline" :tip="hints.penalty" bottom no-link/>
                  </template>
                </input-element>
              </v-row>
            </v-col>

          </v-row>
        </v-form>

        <v-row v-if="mode==='new'" class="ma-5">
          <btn-with-tooltip icon="mdi-chevron-left" :color="validForm?'primary':'secondary'"
                            hover-color="error" bottom
                            :content-class="validForm?'text_primary--text':'text_secondary--text'"
                            text="Use defaults" :click-handler="useDefaults"/>
          <v-spacer/>
          <btn-with-tooltip icon="mdi-check" :color="validForm?'secondary':'primary'"
                            hover-color="success" bottom :disabled="!validForm"
                            :content-class="validForm?'text_secondary--text':'text_primary--text'"
                            append-icon
                            text="Apply" :click-handler="apply"/>
        </v-row>
        <v-row v-else class="ma-5">
          <btn-with-tooltip icon="mdi-chevron-left" :color="validForm?'primary':'secondary'"
                            bottom :content-class="(validForm?'text_primary--text':'text_secondary--text')+' mr-2'"
                            text="Go back" :click-handler="()=>showDialog=false"/>
          <v-spacer/>
          <btn-with-tooltip icon="mdi-check" :color="validForm?'secondary':'primary'"
                            bottom :disabled="!validForm"
                            :content-class="validForm?'text_secondary--text':'text_primary--text'"
                            append-icon
                            text="Generate new analysis" :click-handler="apply"/>
        </v-row>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import InputElement from "@/components/form/InputElement.vue";
import IconWithTooltip from "@/components/general/basic/IconWithTooltip.vue";
import {mapState} from "vuex";

export default {
  name: "ParamSelector",
  components: {IconWithTooltip, InputElement, BtnWithTooltip},

  props: {
    /** Object of the form {bins, penalty} */
    value: {required: true},

    /** Mode of the activator button. Either 'new' or 'info' */
    mode: {default: "new"}
  },

  data() {
    return {

      /** Visibility flag for the form */
      showDialog: false,

      /** Detailed params descriptions TODO: update params hints  */
      hints: {
        bins: 'This is a possibly long and detailed description of this field',
        penalty: 'This is a possibly long and detailed description of this field',
      },

      /** Validation rules TODO: update rules*/
      rules: {
        bins: [
          val => !!val || 'Required.',
          val => (Number.isInteger(Number(val)) && val > 0) || 'Must be a positive integer',
        ],
        penalty: [
          val => !!val || 'Required.',
          val => (!Number.isNaN(Number(val))) || 'Must be a valid number',
        ]
      },

      /** Boolean flag set to true if the form is valid */
      validForm: null,
    }
  },

  computed: {
    ...mapState(['analysisAttempt', 'defaultParams']),

    /** Number of bins */
    bins: {
      set(newVal) {
        this.$emit('input', {bins: newVal, penalty: this.value.penalty})
      },
      get() {
        return this.value.bins
      }
    },

    /** Penalty factor for similar lineages */
    penalty: {
      set(newVal) {
        this.$emit('input', {bins: this.value.bins, penalty: newVal})
      },
      get() {
        return this.value.penalty
      }
    },
  },

  methods: {

    /**
     * Restore specified field to default value
     * @param field Field name to be cleared
     */
    clear(field) {
      if (field === 'bins')
        this.bins = this.defaultParams.bins
      else
        this.penalty = this.defaultParams.penalty
    },

    /**
     * Use default options
     */
    useDefaults() {
      this.$emit('input', {bins: this.defaultParams.bins, penalty: this.defaultParams.penalty})
      this.showDialog = false
    },

    /**
     * Apply custom options
     */
    apply() {
      this.$emit('configured')
      this.showDialog = false
    }
  },
}
</script>

