<!--

  Component:    TitleEditor
  Description:  Editor for the title

-->

<template>
  <v-col >
    <v-list color="transparent">
      <v-list-item class="px-0 pr-5">
        <v-list-item-icon>
          <v-icon left large color="tertiary">mdi-format-title</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title class="break-spaces font-weight-bold tertiary--text">Analysis title</v-list-item-title>
          <v-list-item-subtitle v-if="editIdMode" class="text-body-4 break-spaces">
            <div>
              <v-form v-model="validForm" @submit.prevent="">
                <v-text-field v-model="newTitle" class="pt-0" dense :rules="rules">
                  <template v-slot:append-outer>
                    <icon-with-tooltip icon="mdi-check" hover-color="success" tip="Apply changes" :disabled="!validForm"
                                       :click-handler="apply" bottom/>
                    <icon-with-tooltip icon="mdi-arrow-u-left-top" tip="Hide"
                                       :click-handler="()=>editIdMode=false" bottom/>
                  </template>
                </v-text-field>
              </v-form>
            </div>
          </v-list-item-subtitle>
          <v-list-item-subtitle v-else class="break-spaces mb-3" v-text="title"/>

          <btn-with-tooltip v-if="!editIdMode" bottom
                            tip="Assign/change identifier. The sequence identifier is used as analysis title."
                            :text="title?'Rename':'Assign'" append-icon
                            icon="mdi-pencil" size="x-small" outlined :click-handler="()=>editIdMode=true"/>

        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-col>
</template>

<script>
import {mapGetters, mapMutations} from "vuex";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import IconWithTooltip from "@/components/general/basic/IconWithTooltip.vue";

export default {
  name: "TitleEditor",
  components: {IconWithTooltip, BtnWithTooltip},

  data() {
    return {
      /** Validation rules TODO: update rules*/
      rules: [
        val => !!val || 'Required.',
      ],

      /** Boolean flag set to true if the form is valid */
      validForm: null,

      /** Analysis title */
      newTitle: null,

      /** Boolean flag set to true if id editing is enabled */
      editIdMode: false,
    }
  },

  watch: {
    editIdMode(newVal) {
      if (newVal)
        this.newTitle = this.getCurrentAnalysis.title
    }
  },

  computed: {
    ...mapGetters(['getCurrentAnalysis']),

    /** Analysis title */
    title() {
      return this.getCurrentAnalysis.title
    }
  },

  methods: {
    ...mapMutations(['setTitle']),

    /** Apply changes */
    apply(){
      this.setTitle(this.newTitle)
      this.editIdMode=false
    }
  },
}
</script>

