<!--

  Component:    RecurrencePlot
  Description:  Barchart plot. Implemented using vue-plotly.

-->

<template>
  <section-element icon="mdi-chart-bar" assign-id="recurrence-plot"
                   title="Recurrence Plot"
                   subtitle="Recurrence probability for each pair of candidate recombinant lineages"
                   caption="Higher values are associated with a higher probability that the sequence is the result of recombination of the lineage pair">
    <div class='regular-plot plotly-container px-1' id="recurrence-plot-body">

      <!-- Plot controls -->
      <div class="plot-controls">
        <!-- Pair limit controller -->
        <plot-pair-limit-control v-model="limit"/>

        <!-- Plot description-->
        <recurrence-plot-info/>

        <!-- Generate similarity analysis -->
        <similarity-analysis-generator v-model="askBins" :preselected-pair="clickedPair"/>

      </div>

      <!-- Actual plot -->
      <plotly ref="recurrencePlot" id="recurrence-plot-plotly" :data='data' :layout='layout' :displaylogo='false'
              class="px-2" :modeBarButtonsToRemove="['lasso2d', 'select2d', 'toggleSpikelines','toImage']"
              :modeBarButtonsToAdd="[downloadImg]" @click="onClick"/>

      <loading-sticker :is-loading="downloadLoading" :loading-messages="[{text:'Download in progress'}]"/>
    </div>
    </section-element>
</template>

<script>
import {Plotly} from '@rleys/vue-plotly';
import SectionElement from "@/components/analysis/SectionElement";
import {mapGetters} from "vuex";
import {palette} from "@/utils/colorService";
import PlotPairLimitControl from "@/components/analysis/plots/controls/PlotPairLimitControl.vue";
import RecurrencePlotInfo from "@/components/analysis/plots/controls/RecurrencePlotInfo.vue";
import SimilarityAnalysisGenerator from "@/components/analysis/plots/controls/SimilarityAnalysisGenerator.vue";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import {takeScreenshot} from "@/utils/screenshotService";

export default {
  name: 'RecurrencePlot',
  components: {
    PlotPairLimitControl, LoadingSticker, RecurrencePlotInfo, SectionElement,
    SimilarityAnalysisGenerator, Plotly
  },

  data() {
    return {
      /** Flag set to true if download is in progress */
      downloadLoading: false,

      /** Number of pairs to be shown */
      limit: 10,

      /** Number of pairs to be shown */
      askBins: false,

      /** Clicked pair of lineages or null */
      clickedPair: null
    }
  },

  computed: {
    ...mapGetters(['getCurrentAnalysis', 'getRecurrenceData']),

    /** Download as image */
    downloadImg() {
      const fileName=this.getCurrentAnalysis.title + ' recurrence'
      return takeScreenshot(document, 'recurrence-plot-body', fileName, () => {
        this.downloadLoading = !this.downloadLoading
      })
    },

    /** Filtered recurrence data based on the limit value */
    filteredData() {
      return this.getRecurrenceData.slice(0, (this.limit !== -1) ? this.limit : undefined)
    },

    /** Data processed for the plot */
    data() {
      return [
        {
          y: this.filteredData.map(({lineages}) => lineages.join(' + ')),
          x: this.filteredData.map(({score}) => score * 100),
          hoverlabel: {
            bordercolor: 'transparent',
            font: {
              color: this.filteredData.map(({color}) => (color.isDark ? '#FFFFFF' : '#000000')),
            }
          },
          marker: {
            color: this.filteredData.map(({color}) => color.color),
          },
          hovertemplate: ' &nbsp; %{y}: %{x}% &nbsp; <extra>&nbsp;Click to view similarity plot</extra>',
          orientation: 'h',
          showlegend: false,
          type: 'bar'
        }
      ]
    },

    /** Layout data for the plot */
    layout() {
      return {
        height: this.filteredData.length * 20 + 250,
        colorway: palette,
        yaxis: {
          title: {
            text: 'Candidate recombinant lineages',
            standoff: 15,
            font: {
              size: 14
            }
          },
          tickfont: {
            size: 13
          },
          automargin: true,
          showline: false,
          zeroline: false
        },
        xaxis: {
          title: {
            text: 'Recurrence measure in %',
            standoff: 12,
            font: {
              size: 14
            }
          },
          range: [0, 100],
          tickfont: {
            size: 13
          },
          zeroline: false,
          showline: false,
          automargin: true
        },
        hovermode: 'closest',
        margin: {
          b: 50,
          t: 55,
          r: 20,
          pad: 10
        },
        autosize: true
      }
    }
  },

  methods: {
    /**
     * On click event ask bins for similarity analysis
     * @param data  String of the form <Lin_1> + <Lin_2>
     */
    onClick(data) {
      if (data?.points[0]?.y) {
        this.clickedPair = data.points[0].y
        this.askBins = true
      }
    }
  },

}
</script>
