<!--

  Component:    SimilarityPlotsSection
  Description:  Section containing multiple similarity plots.

-->

<template>
  <section-element icon="mdi-chart-line" assign-id="similarity-plots"
                   title="Similarity Plots"
                   subtitle="Here in short what this plot is but it has to keep this length"
                   caption="Higher values means .... ">

    <div class='regular-plot plotly-container px-1'>
      <div class="plot-controls">
        <btn-with-tooltip v-if="!dragMode" bottom color="primary" :outlined="!cmpMode" hover-color="secondary"
                          size="small"
                          icon="mdi-select-compare" :text="cmpMode?'Exit':'Compare'"
                          tip="Merge two similarity plots to compare them"
                          :click-handler="cmpClick"/>
        <btn-with-tooltip v-if="!cmpMode" bottom :color="dragMode?'secondary':'primary'" :outlined="!dragMode"
                          hover-color="secondary" size="small"
                          icon="mdi-view-dashboard-edit-outline" :text="dragMode?'Done':'Edit'"
                          tip="Rearrange or delete plots"
                          :click-handler="dragClick"/>
        <similarity-plot-info v-if="!dragMode && !cmpMode"/>
      </div>

      <!-- Actual plot -->
      <v-container fluid class="text_dark--text">

        <!-- Mode headings -->
        <v-expand-transition>
          <v-row v-if="dragMode || cmpMode">
            <v-col class="text-center secondary--text text-body-0 pb-6 font-weight-medium">
              <div v-if="dragMode" class="mb-2">Drags the cells to rearrange them</div>
              <div v-else>
                <div class="mb-2">{{
                    cmpSelection.length != 2
                        ? cmpSelection.length == 0
                            ? 'Select 2 pairs to be compared'
                            : ('Select the pair to compare with ' + cmpSelectionName)
                        : '2 pairs selected'
                  }}
                  <div class="text-body-3 pb-6 font-weight">
                    They will be merged into a single view to allow comparison
                  </div>
                  <btn-with-tooltip v-if="cmpSelection.length===2" text="Compare" icon="mdi-check" color="secondary"
                                    :click-handler="addCmpView"/>
                  <loading-sticker :error="warning" :error-messages="warningMessages"/>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-expand-transition>

        <!-- Grid of plots -->
        <draggable v-if="getSimilarityData.length>0" :disabled="!dragMode" class="mb-13 row" v-model="gridElements">

          <!-- Single plot element -->
          <v-col v-for="{id,pair,color,bins,type,...data} in gridElements" :key="id" :id="'similarity-plot-'+id"
                 cols="12" sm="12" md="6" class="similarity-container">
            <div :class="'inner-container '+(dragMode||cmpMode?'cursor-pointer':'')"
                 @click="manageCellClick((type==='std'?id:null))"
                 :style="'border-color:'+color.color+';'+(dragMode||cmpMode? 'border-width:6px; padding: 17px;':'')">

              <!-- Plot info: title, params, controls -->
              <v-row class="d-flex">
                <div class="ml-3">

                  <!-- Plot title -->
                  <div>
                    <v-icon left :color="color.color">mdi-circle</v-icon>
                    <span class="font-weight-bold text-body-0">{{ [pair[0], pair[1]].join(' + ') }}</span>
                    <span v-if="type==='cmp'"> vs
                      <span class="font-weight-bold text-body-0">{{ [pair[2], pair[3]].join(' + ') }}</span>
                    </span>
                  </div>

                  <!-- Plot params -->
                  <div class="ml-8 text-uppercase letter-spacing-1">
                    <v-chip v-if="type==='std'" color="secondary" small>Bins: {{ bins }}</v-chip>
                    <v-chip v-else color="primary" small>Comparison</v-chip>
                  </div>
                </div>

                <v-spacer/>

                <!-- Plot options -->
                <similarity-plot-options :value='cmpSelection.includes(id)' :plot-id="id" :drag-mode="dragMode"
                                         :cmp-mode="cmpMode"/>

              </v-row>

              <!-- Actual plot -->
              <similarity-plot :plot-id="id" :plot-data="data" :type="type" :pair="pair"/>

            </div>
          </v-col>

        </draggable>

        <!-- Add new plot command -->
        <v-row justify="center">
          <v-col cols="auto" class="pb-13">
            <similarity-analysis-generator v-model="showGenerator" mode="standalone"/>
          </v-col>
        </v-row>
      </v-container>

    </div>

  </section-element>
</template>

<script>
import SectionElement from "@/components/analysis/SectionElement";
import {mapGetters, mapMutations, mapState} from "vuex";
import SimilarityAnalysisGenerator from "@/components/analysis/plots/controls/SimilarityAnalysisGenerator.vue";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";
import draggable from 'vuedraggable'
import SimilarityPlot from "@/components/analysis/plots/SimilarityPlot.vue";
import SimilarityPlotOptions from "@/components/analysis/plots/controls/SimilarityPlotOptions.vue";
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import SimilarityPlotInfo from "@/components/analysis/plots/controls/SimilarityPlotInfo.vue";

export default {
  name: 'SimilarityPlotsSection',
  components: {
    SimilarityPlotInfo, LoadingSticker, SimilarityPlotOptions, SimilarityPlot, BtnWithTooltip,
    SimilarityAnalysisGenerator, SectionElement, draggable
  },

  data() {
    return {
      /** Visibility flag for the plot generators */
      showGenerator: false,

      /** Flag to enable drag of cells */
      dragMode: false,
      /** Flag to enable select to compare */
      cmpMode: false,

      /** Ids of the currently selected values */
      cmpSelection: [],

      /** General purpose warning message */
      warning: undefined,
      warningMessages: {
        405: {
          title: 'Not allowed selection',
          text: 'This type of plot cannot be compared'
        }
      },

      /** Flag to prevent scroll on panel change or at first mount */
      initialMount: true,
    }
  },

  computed: {
    ...mapState(['currentAnalysisId']),
    ...mapGetters(['getSimilarityData']),

    /** Elements of the draggable list */
    gridElements: {
      set(newVal) {
        this.updateSimilarityData(newVal)
      },
      get() {
        return this.getSimilarityData
      }
    },

    cmpSelectionName() {
      if (this.cmpSelection.length > 0)
        return this.getSimilarityData.find(({id}) => id === this.cmpSelection[0]).pair.join(' + ')
      else return null
    }

  },

  watch: {
    currentAnalysisId(newVal) {
      this.initialMount = true // catch panel change events to prevent scrolling behavior
    },

    getSimilarityData(newVal, oldVal) {
      // On addition of new plot, scroll to it
      if (newVal.length > oldVal.length && !this.initialMount) {
        this.$nextTick().then(() => this.$vuetify.goTo('#similarity-plot-' + newVal.at(-1).id))
      }
      this.initialMount = false
    },

  },

  methods: {
    ...mapMutations(['updateSimilarityData', 'addSimilarityCmp']),

    /** Manage click on the cells*/
    manageCellClick(id) {
      if (this.cmpMode) {
        if (id === null) {
          // Not allowed element
          this.warning = {code: 405}
          setTimeout(() => this.warning = undefined, 5000)
        } else {
          // Allowed selectable element
          const idx = this.cmpSelection.indexOf(id)
          if (idx > -1) {
            // Already clicked: deselect
            this.cmpSelection = this.cmpSelection.filter((x, i) => i != idx)
          } else {
            if (this.cmpSelection.length > 1) {
              // Limit reached: de-select last
              this.cmpSelection = [this.cmpSelection[0]]
            }
            this.cmpSelection = [...this.cmpSelection, id]
          }
        }
      }
    },

    /** Add a new plot resulting from the cmp mode */
    addCmpView() {
      this.addSimilarityCmp(this.cmpSelection)
      this.cmpSelection = [] // clear selection
      this.cmpMode = false // disable cmp mode
    },

    /** Manage compare button clicks */
    cmpClick() {
      this.cmpMode = !this.cmpMode
      if (this.cmpMode)
        this.dragMode = false
    },

    /** Manage edit button clicks */
    dragClick() {
      this.dragMode = !this.dragMode
      if (this.dragMode)
        this.cmpMode = false
    }
  },

  mounted() {
    this.gridElements = [...this.getSimilarityData]
    this.initialMount = true
  }
}
</script>
<style scoped>

/* Button spacing options */
.plot-controls {
  padding: 10px;
  gap: 5px;
  width: 100%;
  display: inline-flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.similarity-container {
  padding: 5px !important;
  background-color: white;
}

.similarity-container .inner-container {
  padding: 20px;
  border-radius: 15px;
  border: 3px solid var(--v-bg-base);
}
</style>