<!--

  Component:    NotesEditor
  Description:  Note editor

-->

<template>
  <v-col cols="12">
    <v-list color="transparent">
      <v-list-item class="px-0 pr-5">
        <v-list-item-icon>
          <v-icon left large color="tertiary">mdi-note-edit-outline</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title class="font-weight-bold tertiary--text">Notes</v-list-item-title>
          <v-expand-transition hide-on-leave>

            <v-list-item-subtitle v-if="notes || editNoteMode">
              <v-textarea outlined v-model="notes" auto-grow rows="1" clearable clear-icon="mdi-backspace-outline"
                          hide-details color="tertiary"
                          @click:clear="editNoteMode=false"/>
            </v-list-item-subtitle>
            <v-list-item-subtitle v-else>
              <btn-with-tooltip bottom tip="Add notes related to the current analysis" text="Add notes" append-icon
                                icon="mdi-pencil" size="x-small" outlined :click-handler="()=>editNoteMode=true"/>
            </v-list-item-subtitle>

          </v-expand-transition>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-col>
</template>

<script>
import {mapGetters, mapMutations} from "vuex";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: "NotesEditor",
  components: {BtnWithTooltip},

  data() {
    return {
      /** Boolean flag set to true if note editing is enabled */
      editNoteMode: false,
    }
  },

  computed: {
    ...mapGetters(['getCurrentAnalysis']),

    /** Notes for the current analysis */
    notes: {
      set(newVal) {
        this.setNotes(newVal)
      },
      get() {
        return this.getCurrentAnalysis.notes
      }
    },

  },
  methods: {
    ...mapMutations(['setNotes']),
  }
}
</script>

