<!--

  Component:    InputElement
  Description:  Text field input element

  Props:
   label:        Text of the label header
   description:  Description in the header
   hint:         Hint text
   defaultValue: Default value
   noClear:      Flag set to true to disable clear feature
   hideDetails:  Flag set to true to hide details
   placeholder:  Input placeholder
   rules:        Validation function
   value:        Value of the content of the file

-->

<template>
  <v-col>

    <!-- Headings -->
    <v-row class="px-5 pb-2">
      <v-col>
        <div class="text-body-2 compact-text-2 text_bg--text">
          <span class="text-body-1 font-weight-bold tertiary--text">{{ label }}</span>
          {{ description }}
        </div>
      </v-col>
    </v-row>

    <!-- Input element -->
    <v-row dense class="px-5">
      <v-col>
        <v-text-field v-model="input" :placeholder="placeholder" dense outlined color="tertiary" :rules="rules"
                      :hint="hint" :persistent-hint="Boolean(hint)" :hide-details="hideDetails">
          <template v-slot:append v-if="String(input)!==String(defaultValue) && !noClear">
            <v-icon @click="$emit('clear')" class="cursor-pointer">mdi-restore</v-icon>
          </template>
          <template v-slot:append-outer>
            <div class="align-info">
              <slot name="append-outer"></slot>
            </div>
          </template>
        </v-text-field>
      </v-col>
    </v-row>

    <loading-sticker :error="error"/>

  </v-col>
</template>

<script>
import LoadingSticker from "@/components/general/basic/LoadingSticker";

export default {
  name: 'InputElement',
  components: {LoadingSticker},

  props: {
    /** Text of the label header */
    label: {},

    /** Description in the header */
    description: {},

    /** Hint text */
    hint: {},

    /** Default value */
    defaultValue: {},

    /** Input placeholder */
    placeholder: {},

    /** Flag set to true to disable clear feature */
    noClear: Boolean,

    /** Flag set to true to hide details */
    hideDetails: Boolean,

    /** Validation function */
    rules: {default: () => []},

    /** Value of the content of the file */
    value: {}
  },

  data() {
    return {

      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    /** User uploaded file content  */
    input: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    }

  }
}
</script>

<style scoped>
.align-info {
  position: relative;
  bottom: 7px;
}
</style>
