<!--

  Component:    MutationListUploader
  Description:  Form to provide the list of mutations

  Props:
   value:   List value

-->

<template>
  <v-col>

    <!-- Separator heading -->
    <v-row class="px-5 pb-2">
      <v-col>
        <div class="text-body-2 compact-text-2 text_bg--text">
          <span class="text-body-1 font-weight-bold tertiary--text">Separator:</span>
          separator character for the list
        </div>
      </v-col>
    </v-row>

    <!-- Separator input -->
    <v-row dense class="px-5">
      <v-col>
        <v-select v-model="separator" placeholder="Select from the list" :items="supportedSeparators" dense outlined
                  color="tertiary" :rules="separatorRules"/>
      </v-col>
    </v-row>

    <!-- List heading -->
    <v-row class="px-5 pb-2" no-gutters>
      <v-col cols="12">
        <div class="text-body-2 compact-text-2 text_bg--text">
          <span class="text-body-1 font-weight-bold tertiary--text">Mutation list:</span>
          provide the list of nucleotide mutations in the sequence
        </div>
      </v-col>
      <!-- Notation info -->
      <v-col cols="12">
        <v-list color="bg" class="rounded-xl" rounded dense width="auto">
          <v-list-item v-for="({title, text, icon},idx) in notation" dense :key="idx">
            <v-icon class="pr-3" color="tertiary" left>{{ icon }}</v-icon>
            <v-list-item-content>
              <v-list-item-title class="tertiary--text break-spaces font-weight-medium" v-text="title"/>
              <v-list-item-subtitle class="break-spaces notation" v-html="text"/>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-col>
    </v-row>

    <!-- List input element -->
    <v-row dense class="px-5" no-gutters>
      <v-col>
        <v-textarea v-model="textList" dense outlined color="tertiary"
                    :placeholder="'Write here the list of '+supportedSeparators.find(({value})=>value===separator).name+'-separated mutations'"
                    :hint="this.mutations.length +' valid mutations detected'"
                    :error-messages="errorMessages"
                    :loading="isLoading" :error-count="5" @keyup="parseList"/>

      </v-col>
    </v-row>

    <!-- Parsed list -->
    <v-row class="px-5" >
      <v-col>
        <v-chip v-for='elem in elementList' :key='elem' class="mr-1 mb-1"
                :text-color='mutations.includes(elem)?"success darken-3":"error darken-3"'
                :color='mutations.includes(elem)?"success lighten-5":"error lighten-5"' v-text="elem"/>
      </v-col>
    </v-row>

    <loading-sticker :error="error"/>


  </v-col>
</template>

<script>

import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";

export default {
  name: "MutationListUploader",
  components: {LoadingSticker},

  props: {
    /** List of nucleotide mutations */
    value: {}
  },

  data() {
    return {
      separator: ',',
      supportedSeparators: [
        {text: 'Period symbol (.)', value: '.', name: 'period'},
        {text: 'Comma symbol (,)', value: ',', name: 'comma'},
        {text: 'Semicolon symbol (;)', value: ';', name: 'semicolon'},
        {text: 'Single space ( )', value: ' ', name: 'space'},
      ],
      separatorRules: [],

      textList: "",
      elementList: [],
      knownValidElements: new Set(),

      /** Form error messages */
      errorMessages: [],

      /** Options to load sequence */
      notation: [
        {
          icon: 'mdi-autorenew',
          title: 'Substitutions',
          text: '&lt;position&gt;<b>_</b>&lt;base&gt;<b>|</b>&lt;new-base&gt;'
        },
        {
          icon: 'mdi-minus-thick',
          title: 'Deletion',
          text: '&lt;position&gt;<b>_</b>&lt;base&gt;<b>|.</b>'
        },
        {
          icon: 'mdi-plus-thick',
          title: 'Addition',
          text: '&lt;position&gt;<b>_</b><b>.|</b>&lt;new-base&gt;'
        },
      ],

      /** Boolean loading flag for the table and expansion */
      isLoading: false,
      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    /**
     * List of valid and legal mutations
     */
    mutations: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    }
  },

  methods: {
    /**
     * Parse the text into list of valid mutations
     */
    async parseList() {
      // Split, trim and remove duplicates
      let values = this.textList.split(this.separator).map(x => x.toUpperCase().trim())
      this.elementList = values.filter((x, index) => x !== '' && values.indexOf(x) === index)

      // check existence via server
      const mutVerifier = async (muts) => {
        const valid = [], invalid = []
        for (let mut of muts) {
          if(this.knownValidElements.has(mut)){
            valid.push(mut)
            continue
          }

          const apiURL = '/isValidMutation' // TODO: isValidMutation API
          await this.$axios.get(apiURL, {params: {mutation: mut}})
              .then(() => {
                this.knownValidElements.add(mut) // cache locally
                valid.push(mut)
                return true
              })
              .catch((e) => {
                // TODO remove lines below
                if( Math.random() < 0.5){
                  this.knownValidElements.add(mut) // cache locally
                  valid.push(mut)
                  return true
                }else{e.code=404}
                // TODO remove lines above
                if (e.code === 404) {
                  invalid.push(mut)
                  return false
                } else {
                  this.error = e
                }
              })
        }
        return {valid, invalid}
      };

      const {valid, invalid} = await mutVerifier(this.elementList);

      // Add valid ones to the result and invalid to the errors
      this.mutations = valid
      this.errorMessages = []
      invalid.forEach(e => this.errorMessages.push('Mutation "' + e + '" does not exist and will not be considered'))

    }
  }
}
</script>

<style scoped>
#app .notation {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: monospace, monospace !important;
}
</style>
<style>
.notation b {
  letter-spacing: 4px;
  font-weight: bolder;
  color: var(--v-tertiary-base);
}
</style>