<!--

  Component:    SelectElement
  Description:  Text field input element

  Props:
   label:        Text of the label header
   description:  Description in the header
   hint:         Hint text
   items:        Select items
   defaultValue: Default value
   noClear:      Flag set to true to disable clear feature
   returnObj:    Flag set to true to return the obj
   hideDetails:  Flag set to true to hide details
   placeholder:  Input placeholder
   rules:        Validation function
   value:        Value of the content of the file

  Slots:
   selection:    Vuetify selection slot
   items:        Vuetify items slot

-->

<template>
  <v-col>

    <!-- Headings -->
    <v-row class="px-5 pb-2">
      <v-col>
        <div class="text-body-2 compact-text-2 text_bg--text">
          <span class="text-body-1 font-weight-bold tertiary--text">{{ label }}</span>
          {{ description }}
        </div>
      </v-col>
    </v-row>

    <!-- Input element -->
    <v-row dense class="px-5">
      <v-col>
        <v-select v-model="input" :placeholder="placeholder" :items="items" dense outlined
                  color="tertiary" :rules="rules" :hide-details="hideDetails" :return-object="returnObj">
          <template v-slot:item="{item}">
            <div :class="input===(typeof(item)==='object'? item.value: item)?'text_bg--text':''">
              <slot name="item" v-bind:item="item">
                {{ typeof (item) === 'object' ? item.text : item }}
              </slot>
            </div>
          </template>
          <template v-slot:selection="{item}">
            <div :class="input===(typeof(item)==='object'? item.value: item)?'text_bg--text':''">
              <slot name="selection" v-bind:item="item">
                {{ typeof (item) === 'object' ? item.text : item }}
              </slot>
            </div>
          </template>
        </v-select>
      </v-col>
    </v-row>

    <loading-sticker :error="error"/>

  </v-col>
</template>

<script>
import LoadingSticker from "@/components/general/basic/LoadingSticker";

export default {
  name: 'SelectElement',
  components: {LoadingSticker},

  props: {
    /** Text of the label header */
    label: {},

    /** Description in the header */
    description: {},

    /** Select items */
    items: {},

    /** Hint text */
    hint: {},

    /** Default value */
    defaultValue: {},

    /** Input placeholder */
    placeholder: {},

    /** Flag set to true to return the obj */
    returnObj: Boolean,

    /** Flag set to true to disable clear feature */
    noClear: Boolean,

    /** Flag set to true to hide details */
    hideDetails: Boolean,

    /** Validation function */
    rules: {default: () => []},

    /** Value of the content of the file */
    value: {}
  },

  data() {
    return {

      /** Error data for the table and expansion. Undefined if no error. */
      error: undefined,
    }
  },

  computed: {
    /** User uploaded file content  */
    input: {
      set(newVal) {
        this.$emit('input', newVal)
      },
      get() {
        return this.value
      }
    }

  }
}
</script>

<style scoped>
.align-info {
  position: relative;
  bottom: 7px;
}
</style>
