<!--

  Component:    MutationTable
  Description:  Table of chips reporting the list of mutations found
                in the sequence of the analysisAttempt

-->

<template>
  <v-col>

    <!-- Mutation list -->
    <v-row dense>
      <v-col>
        <v-card elevation="0" color="transparent">

          <!-- Heading, search in the list and download -->
          <v-card-title :class="'pt-0 '+(mode!=='result'?'mx-8':'pr-0')">
            <!-- Count heading -->
            <template v-if="mode!=='result'">
              <div class="text-body-2 compact-text-2 text_bg--text">
                <span class="font-weight-bold text-body-0">{{ muts.length }}</span>
                mutation{{ muts.length !== 1 ? 's' : '' }} detected in your sequence
              </div>
              <v-spacer/>
            </template>

            <!-- Search -->
            <v-sheet v-if="muts.length>10" class="hidden-sm-and-down" max-width="300px" color="transparent">
              <v-text-field v-model="search" append-icon="mdi-magnify" placeholder="Search mutation" single-line
                            hide-details dense background-color="transparent" color="secondary" clearable
                            :class="'text-body-4 '+(mode!=='result'?'text_bg--text':'text_dark--text')"
                            :light="(mode==='result'?true:undefined)"/>
            </v-sheet>

            <!-- Downlaod -->
            <template v-if="mode!=='overview'">
              <v-spacer/>
              <btn-with-tooltip bottom color="primary" outlined hover-color="secondary" size="small"
                                icon="mdi-download" text="Download" tip="Download mutation list as .txt file"
                                :click-handler="downloadAsTxt" :loading="downloadLoading"/>
            </template>
          </v-card-title>

          <v-window v-if="shownMuts.length>0" class="pt-2 px-13">
            <v-window-item>
              <v-card color="transparent" flat ref="mutList">
                <v-row class="justify-center" no-gutters>
                  <v-chip v-if="shownMuts.length>itemsPerPage" :disabled="pageStartIndex===0" x-small class="mb-2"
                          @click=prevPage>
                    <v-icon>mdi-chevron-up</v-icon>
                  </v-chip>

                  <v-col class="pt-3 pb-0" cols="12">
                    <v-chip v-for="mut in shownMuts.slice(pageStartIndex,pageStartIndex+itemsPerPage)"
                            :key="mut" class="mr-3 mb-3" :color="getLabelColor(mut).color"
                            :light="!getLabelColor(mut).isDark" :small="mode!=='result'"
                            :dark="getLabelColor(mut).isDark">{{ mut }}
                    </v-chip>
                  </v-col>

                  <v-chip v-if="shownMuts.length>itemsPerPage"
                          :disabled="(pageStartIndex+itemsPerPage)>=shownMuts.length" x-small
                          class="mt-2" @click=nextPage>
                    <v-icon>mdi-chevron-down</v-icon>
                  </v-chip>

                </v-row>
              </v-card>
            </v-window-item>
          </v-window>

          <div v-if="shownMuts.length===0" class="text-body-3 text-center">
            <span v-if="search">No mutations matching `{{ search }}`</span>
            <span v-else>No mutations found in your sequence</span>
          </div>
        </v-card>
      </v-col>
    </v-row>

  </v-col>
</template>

<script>
import {getRandomColor} from "@/utils/colorService";
import {mapGetters, mapState} from "vuex";
import BtnWithTooltip from "@/components/general/basic/BtnWithTooltip.vue";

export default {
  name: 'MutationTable',
  components: {BtnWithTooltip},

  props: {
    /** Mode of use. Either 'overview' (fetch muts from analysisAttempt) or 'result' (fetch muts from analyses) */
    mode: {default: 'overview'}
  },

  data() {
    return {
      /** Number of items per page **/
      itemsPerPage: this.mode !== 'result' ? 20 : 80,
      /** Current page index start **/
      pageStartIndex: 0,

      /** Search string */
      search: '',

      /** Object storing label colors*/
      labelColors: {},

      /** Download status */
      downloadLoading: false,
    }
  },

  computed: {
    ...mapState(['analysisAttempt']),
    ...mapGetters(['getCurrentAnalysis']),

    /** List of all the mutations found */
    muts() {
      return this.mode!=='result' ?this.analysisAttempt.muts :this.getCurrentAnalysis.muts
    },

    /** List of mutations matching search string or all the strings if no search */
    shownMuts() {
      return this.search
          ? this.muts.filter(name => name.toLowerCase().includes(this.search.toLowerCase()))
          : this.muts
    },

  },

  watch: {
    /** On change, reset page position */
    shownMuts() {
      this.pageStartIndex = 0
    }
  },

  methods: {

    /** Next page handler */
    nextPage() {
      this.pageStartIndex += this.itemsPerPage
    },
    /** Prev page handler */
    prevPage() {
      this.pageStartIndex -= this.itemsPerPage
    },

    /** Get a random color to be associated with the label */
    getLabelColor(label) {
      if (!this.labelColors[label])
        this.labelColors[label] = getRandomColor()
      return this.labelColors[label]
    },

    /** Download the mut list as txt file */
    downloadAsTxt(){
      this.downloadLoading = true

      const txt = this.shownMuts.join(', ')
      // Anchor element to download the file
      const anchor = document.createElement('a')
      anchor.setAttribute('download', this.getCurrentAnalysis.title + ' muts.txt')
      const data = new Blob([txt])
      anchor.href = URL.createObjectURL(data)
      document.body.appendChild(anchor)
      // Simulate click and remove element
      anchor.click()
      document.body.removeChild(anchor)
      this.downloadLoading = false
    },
  },

  mounted() {
    // Compute number of items per page based on window size to fill 3 lines
    const width = this.$refs.mutList.$el.clientWidth
    if (width) this.itemsPerPage = Math.floor((width / 85) * 3)
  }
}
</script>

