<!--

  Component:    SimilarityPlot
  Description:  Similarity plot. Implemented using vue-plotly.

  Props:
   plotId:       Plot id
   plotData:     Raw plot data
   pair:         Lineage pair
   type:         Type of plot. Either 'std' or 'cmp'

-->

<template>
  <v-row>
    <v-col class='regular-plot plotly-container px-1'>
      <!-- Actual plot -->
      <plotly ref="main" :data='data' :layout='layout' :displaylogo='false' @relayout="onZoom"
              :modeBarButtonsToRemove="['lasso2d', 'select2d', 'toggleSpikelines','toImage']" :modeBarButtonsToAdd="[downloadImg]" />

      <!-- Annotation plot -->
      <v-expand-transition>
        <plotly v-show="plotAnnotations.length>0" ref="annotations" :data='annotationsData' :layout='annotationsLayout'
                :displaylogo='false' @relayout="onZoom" @click="onAnnotationClick"
                :modeBarButtonsToRemove="['lasso2d', 'select2d', 'toggleSpikelines','toImage','autoScale2d','hoverClosestCartesian','hoverCompareCartesian']"/>
      </v-expand-transition>

      <div class="plot-controls pb-0 pt-3">
        <plot-slider :range="range" @goTo="onZoom"/>
        <v-spacer/>
        <annotate-plot :plot-id="plotId"/>
      </div>
      <loading-sticker :is-loading="downloadLoading" :loading-messages="[{text:'Download in progress'}]"/>

    </v-col>
  </v-row>
</template>

<script>
import {Plotly} from '@rleys/vue-plotly';
import LoadingSticker from "@/components/general/basic/LoadingSticker.vue";
import AnnotatePlot from "@/components/analysis/plots/controls/AnnotatePlot.vue";
import PlotSlider from "@/components/analysis/plots/controls/PlotSlider.vue";
import {mapGetters} from "vuex";
import {takeScreenshot} from "@/utils/screenshotService";

export default {
  name: 'SimilarityPlot',
  components: {Plotly, AnnotatePlot, PlotSlider,LoadingSticker},

  props: {
    /** Plot id */
    plotId: {},

    /** Raw plot data */
    plotData: {},

    /** Lineage pair */
    pair: {},

    /**  Type of plot. Either 'std' or 'cmp' */
    type: {},
  },

  data() {
    return {
      /** Flag set to true if download is in progress */
      downloadLoading: false,

      /** Plot HTML elements */
      divs: [],

      /** Current plot range */
      range: [],
    }
  },

  computed: {
    ...mapGetters(['getCurrentAnalysis','getSimilarityAnnotations']),

    /** Download as image */
    downloadImg() {
      const fileName=this.getCurrentAnalysis.title + ' '+ this.pair.join(', ') +' similarity'
      const id= "similarity-plot-"+this.plotId
      return takeScreenshot(document, id, fileName, () => {
        this.downloadLoading = !this.downloadLoading
      })
    },

    /** Plot annotations for the current plot */
    plotAnnotations() {
      return this.getSimilarityAnnotations[this.plotId]
    },

    /** Data processed for the plot */
    data() {
      return [
        ...this.j1sx,
        ...this.j2dx,
        ...this.c
      ]
    },

    /** J1SX data processed for the plot */
    j1sx() {
      return [...Array(this.type === 'std' ? 1 : 2).keys()].map((idx) => (
          {
            name: 'J(' + this.pair[idx * 2] + ',sx)',
            x: this.plotData.coordinates[idx],
            y: this.plotData.j1sx[idx],
            hoverlabel: {
              bordercolor: 'transparent',
              font: {
                color: (idx === 1 ? '#FFFFFF' : '#000000'),
              }
            },
            marker: {
              color: idx === 1 ? '#061291' : '#2884CE',
            },
            legendgroup: 1,
            showlegend: true,
            mode: 'lines+markers'
          }
      ))
    },

    /** J2DX data processed for the plot */
    j2dx() {
      return [...Array(this.type === 'std' ? 1 : 2).keys()].map((idx) => (
          {
            name: 'J(' + this.pair[1 + idx * 2] + ',dx)',
            x: this.plotData.coordinates[idx],
            y: this.plotData.j2dx[idx],
            hoverlabel: {
              bordercolor: 'transparent',
              font: {
                color: (idx === 1 ? '#FFFFFF' : '#000000'),
              }
            },
            marker: {
              color: idx === 1 ? '#CA1B00' : '#FA2424',
            },
            legendgroup: 2,
            showlegend: true,
            mode: 'lines+markers'
          }
      ))
    },

    /** c data processed for the plot */
    c() {
      return [...Array(this.type === 'std' ? 1 : 2).keys()].map((idx) => (
          {
            name: 'C(' + this.pair[idx * 2] + ' sx ,' + this.pair[1 + idx * 2] + ' dx)',
            x: this.plotData.coordinates[idx],
            y: this.plotData.c[idx],
            hoverlabel: {
              bordercolor: 'transparent',
              font: {
                color: (idx === 1 ? '#FFFFFF' : '#000000'),
              }
            },
            marker: {
              color: idx === 1 ? '#256425' : '#069106',
            },
            legendgroup: 3,
            showlegend: true,
            mode: 'lines+markers'
          }
      ))
    },

    /** Layout data for the plot */
    layout() {
      return {
        height: 410,
        yaxis: {
          title: {
            text: 'Similarity measure',
            standoff: 15,
            font: {
              size: 14
            }
          },
          tickfont: {
            size: 13
          },
          range: [0, 2],
          automargin: true,
          showline: false,
          zeroline: false
        },
        xaxis: {
          title: {
            text: this.plotAnnotations.length===0? 'Gene positions': undefined,
            standoff: 12,
            font: {
              size: 14
            }
          },
          tickfont: {
            size: 13
          },
          range: [0, 29903],
          zeroline: false,
          showline: false,
          automargin: true
        },
        legend: {
          orientation: 'h',
          yanchor: "bottom",
          y: 1.02,
        },
        hovermode: 'closest',
        margin: {
          b: 50,
          t: 55,
          l: 0,
          r: 10,
          pad: 10
        },
        autosize: true
      }
    },

    /** Annotations plot data */
    annotationsData() {
      return this.plotAnnotations.map(({displayName, line, start, stop}) => ({
        name: displayName,
        mode: 'lines+text',
        x: [start, stop, stop, start, start],
        y: [line, line, line + 1, line + 1, line],
        hovertemplate: displayName + ' <extra>Click to center on ' + displayName + '</extra>',
        text: ((stop - start) / (this.range[1] - this.range[0]) >= displayName.length * 0.0126) ? [' ' + displayName] : [],
        textposition: 'top-right',
        fill: 'toself'
      }))
    },

    /** Annotation plot layout */
    annotationsLayout() {
      return {
        height: 150,
        yaxis: {
          fixedrange: true,
          showticklabels: false,
          showgrid: false,
          automargin: true,
          showline: false,
          zeroline: false
        },
        xaxis: {
          title: {
            text: 'Gene positions',
            standoff: 12,
            font: {
              size: 14
            }
          },
          range: [0, 29903],
          zeroline: false,
          showline: false,
          automargin: true
        },
        showlegend: false,
        hovermode: 'closest',
        margin: {
          b: 50,
          t: 0,
          l: 58,
          r: 5,
          pad: 10
        },
        autosize: true,
      }
    }
  },

  watch: {
    range(newVal) {
      if (newVal && newVal.at(0) < 0) {
        const ed = {
          "xaxis.range[0]": 0,
          "xaxis.range[1]": 29903
        }
        this.onZoom(ed)
      }
    },
  },

  methods: {
    /**
     * Synchronize pltos range on zoom events
     * @param ed  Relayout event
     */
    onZoom(ed) {
      this.divs.forEach((div, i) => {
        let x = div.$el.layout.xaxis;
        let update = {};

        if ("xaxis.autorange" in ed && ed["xaxis.autorange"] != x.autorange) {
          update['xaxis.autorange'] = ed["xaxis.autorange"];
          this.range = [...x.range]
          return
        }
        if ("xaxis.range[0]" in ed && ed["xaxis.range[0]"] != x.range[0]) {
          update['xaxis.range[0]'] = ed["xaxis.range[0]"];
        }
        if ("xaxis.range[1]" in ed && ed["xaxis.range[1]"] != x.range[1]) {
          update['xaxis.range[1]'] = ed["xaxis.range[1]"];
        }

        if (Object.keys(update).length > 0)
          div.relayout(update); // replot if necessary

        if (x?.range) {
          this.range = [...x.range] // store range
        }

      });
    },

    /**
     * Manages annotations click and center on the protein
     * @param e Plotly click event
     */
    onAnnotationClick(e) {
      // Prevent double clicks
      if (e.event.detail > 1) return

      const annotation = e?.points[0].data.name
      const annotationData = this.plotAnnotations
          .find(({displayName}) => displayName === annotation) // fetch annotation data

      const ed = {
        "xaxis.range[0]": annotationData.start,
        "xaxis.range[1]": annotationData.stop
      }
      this.onZoom(ed) // zoom plot and synch
    },
  },

  mounted() {
    this.divs = []
    this.divs.push(this.$refs.main)
    this.divs.push(this.$refs.annotations)
  }

}
</script>
